
<template>

  <div class="slide-billing">

    <div class="slide-content-wrapper kite-container">
      <div class="content">

        <section class="section-payment">
          <div class="section-content">
            <h1>A Better Way to Pay</h1>
            <p class="kite-lead">Save time and never miss a payment by enrolling in Auto Pay. Your balance will be paid automatically every month with the payment method you provide.</p>
            <div v-html="extraCopy"></div>

            <div class="payment-info-box">
              <div class="title-row">
                <h4>Auto Pay Info</h4>
                <a :href="require('../../assets/img/autopay-bill.pdf')" target="_blank" class="kite-btn kite-btn-standalone-link">View Bill</a>
              </div>

              <div
                class="loader-overlay"
                :class="{show: showLineItemLoader}"
              >
                <div class="loader-position">
                  <Loader></Loader>
                </div>
              </div>

              <template v-if="!skipFirstPayment">
                <div
                  v-if="billStatus === 'balance' || billStatus === 'pastdue' || billStatus === 'balance-cutoff'"
                  class="line-item"
                >
                  <span class="title">First Payment Amount</span>
                  <span class="underline-connector"></span>
                  <span class="value">$123.45</span>
                </div>
                <div
                  v-if="billStatus === 'none' || billStatus === 'balance'"
                  class="line-item"
                >
                  <span class="title">First Payment Date</span>
                  <span class="underline-connector"></span>
                  <span class="value">{{paymentDate}}</span>
                </div>
                <div
                  v-if="billStatus === 'none'"
                  class="line-item"
                >
                  <span class="title">Next Payment Date</span>
                  <span class="underline-connector"></span>
                  <span class="value">On or around {{nextPaymentDate}}</span>
                </div>
                <div
                  v-if="billStatus === 'pastdue' || billStatus === 'balance-cutoff'"
                  class="line-item"
                >
                  <span class="title">First Payment Date</span>
                  <span class="underline-connector"></span>
                  <span class="value">Today: {{today}}</span>
                </div>
              </template>


              <!-- Alrady paid lines -->
              <template v-if="skipFirstPayment">
                <div
                  class="line-item"
                >
                  <span class="title">First Payment Date</span>
                  <span class="underline-connector"></span>
                  <span class="value">On or around {{nextPaymentDate}}</span>
                </div>
                <div
                  class="line-item"
                >
                  <span class="title">Next Payment Date</span>
                  <span class="underline-connector"></span>
                  <span class="value">On or around {{skipPaymentNextDate}}</span>
                </div>
              </template>

            </div>

            <div class="auto-pay">

              <div class="payment-info">
                <div class="payment-types">
                  <label
                    for="autopay-checking"
                    class="kite-custom-control kite-custom-radio"
                    :class="{selected: (paymentInfo.paymentMethod === 'checking')}"
                  >
                    <input
                      type="radio"
                      name="autopay-type"
                      id="autopay-checking"
                      class="kite-custom-control-input"
                      value="checking"
                      v-model="paymentInfo.paymentMethod"
                      @change="onPaymentMethodChange"
                      ref="checkingRadio"
                    >
                    <div class="kite-custom-control-indicator"></div>
                    <span class="kite-custom-control-description">
                      Checking Account
                    </span>
                  </label>

                  <label
                    for="autopay-savings"
                    class="kite-custom-control kite-custom-radio"
                    :class="{selected: (paymentInfo.paymentMethod === 'savings')}"
                  >
                    <input 
                      type="radio" 
                      name="autopay-type" 
                      id="autopay-savings" 
                      class="kite-custom-control-input"
                      value="savings" 
                      v-model="paymentInfo.paymentMethod"
                      @change="onPaymentMethodChange"
                    >
                    <div class="kite-custom-control-indicator"></div>
                    <span class="kite-custom-control-description">
                      Savings Account
                    </span>
                  </label>

                  <label
                    for="autopay-credit"
                    class="kite-custom-control kite-custom-radio"
                    :class="{selected: (paymentInfo.paymentMethod === 'credit')}"
                  >
                    <input 
                      type="radio" 
                      name="autopay-type"
                      id="autopay-credit" 
                      class="kite-custom-control-input"
                      value="credit" 
                      v-model="paymentInfo.paymentMethod"
                      @change="onPaymentMethodChange"
                    >
                    <div class="kite-custom-control-indicator"></div>
                    <span class="kite-custom-control-description">
                      Credit/Debit Card
                    </span>
                  </label>

                  <div class="selected-underline"></div>
                </div>


                <div class="payment-details">

                  <transition
                    name="fast-fade"
                    mode="out-in"
                  >

                    <!-- Credit Card -->
                    <div
                      class="transition-container"
                      v-if="paymentInfo.paymentMethod === 'credit'"
                      key="credit"
                    >
                      <div class="details-container credit-details">

                        <label for="card-number">
                          <span class="label">Card Number</span>

                          <span class="number-icon-container">
                            <cleave-input
                              :options="{
                                creditCard: true,
                                onCreditCardTypeChanged: onPaymentCardTypeChange
                              }"
                              :value="paymentInfo.card"
                              :className="(!cardValid) ? 'kite-form-control-danger' : ''"
                              v-on:cleave-input="onPaymentCardValueChange"
                              id="card-number"
                            ></cleave-input>
                            <span
                              class="card-icon-indicator"
                              :class="{
                                error: !cardValid,
                                empty: (paymentInfo.creditCardType === '' && cardValid)
                              }"
                            >
                              <img
                                src="../../assets/img/card-visa.svg"
                                :class="{active: paymentInfo.creditCardType === 'visa' }"
                                alt="visa"
                              >
                              <img
                                src="../../assets/img/card-mastercard.svg"
                                :class="{active: paymentInfo.creditCardType === 'mastercard' }"
                                alt="mastercard"
                              >
                              <img
                                src="../../assets/img/card-discover.svg"
                                :class="{active: paymentInfo.creditCardType === 'discover' }"
                                alt="discover card"
                              >
                              <img
                                src="../../assets/img/card-amex.svg"
                                :class="{active: paymentInfo.creditCardType === 'amex' }"
                                alt="american express"
                              >
                              <img
                                src="../../assets/img/card-error.svg"
                                class="error-card"
                                :class="{active: !cardValid }"
                                alt="card-type error icon"
                              >
                              <img
                                src="../../assets/img/card-default.svg"
                                :class="{active: paymentInfo.creditCardType === '' && cardValid }"
                                alt="default credit card"
                              >
                            </span>
                          </span>
                          <span
                            class="danger-message"
                            :class="{show: !cardValid}"
                          >
                            Please enter a valid card number.
                          </span>
                        </label>

                        <label for="exp-date">
                          <span class="label">Expiration Date</span>
                          <cleave-input
                            :options="{
                              date: true,
                              datePattern: ['m', 'y']
                            }"
                            :value="paymentInfo.cardExpiration"
                            :className="(!cardExpireValid) ? 'kite-form-control-danger' : ''"
                            v-on:cleave-input="onPaymentCardExpChange"
                            id="exp-date"
                            placeholder="MM/YY"
                          ></cleave-input>
                          <span
                            class="danger-message"
                            :class="{show: !cardExpireValid}"
                          >
                            Please enter an expiration date
                          </span>
                        </label>

                      </div>
                    </div>
                    <!-- // end credit // -->


                    <!-- Checking Account -->
                    <div
                      class="transition-container"
                      v-if="paymentInfo.paymentMethod === 'checking'"
                      key="checking"
                    >
                      <div class="details-container checking-details">
                        <label for="check-routing">
                          <span class="label">
                            Routing Number
                            <tooltip 
                              v-if="isMobile"
                              :tip="routingTip" 
                              :isLight="true"
                            ></tooltip>
                          </span>
                          <input
                            type="number"
                            id="check-routing"
                            name="check-routing"
                            maxlength="9"
                            :value="paymentInfo.checkRouting"
                            @input="onCheckingRouteChange"
                            @blur="checkRoutingTouched = true"
                            class="kite-form-control"
                            :class="{'kite-form-control-danger': !checkRoutingValid}"
                          >
                          <span
                            class="danger-message"
                            :class="{show: !checkRoutingValid}"
                          >
                            Please enter a valid 9-digit routing number.
                          </span>
                        </label>

                        <label for="check-account">
                          <span class="label">
                            Account Number
                            <tooltip
                              v-if="isMobile"
                              :tip="accountTip"
                              :isLight="true"
                            ></tooltip>
                          </span>
                          <input
                            type="number"
                            id="check-account"
                            name="check-account"
                            :value="paymentInfo.checkAccount"
                            @input="onCheckingAcctChange"
                            @blur="checkAccountTouched = true"
                            class="kite-form-control"
                            :class="{'kite-form-control-danger': !checkAccountValid}"
                          >
                          <span
                            class="danger-message"
                            :class="{show: !checkAccountValid}"
                          >
                            Please enter a valid account number.
                          </span>
                        </label>
                      </div>
                    </div>
                    <!-- // end checking // -->


                    <!-- Savings Account -->
                    <div
                      class="transition-container"
                      v-if="paymentInfo.paymentMethod === 'savings'"
                      key="savings"
                    >
                      <div class="details-container savings-details">
                        <label for="saving-routing">
                          <span class="label">
                            Routing Number
                            <tooltip
                              v-if="isMobile"
                              :tip="routingTip"
                              :isLight="true"
                            ></tooltip>
                          </span>
                          <input
                            type="number"
                            id="saving-routing"
                            name="saving-routing"
                            maxlength="9"
                            :value="paymentInfo.savingRouting"
                            @input="onSavingRouteChange"
                            @blur="savingRoutingTouched = true"
                            class="kite-form-control"
                            :class="{'kite-form-control-danger': !saveRoutingValid}"
                          >
                          <span
                            class="danger-message"
                            :class="{show: !saveRoutingValid}"
                          >
                            Please enter a valid 9-digit routing number.
                          </span>
                        </label>
                        <label for="saving-account">
                          <span class="label">
                            Account Number
                            <tooltip
                              v-if="isMobile"
                              :tip="accountTip"
                              :isLight="true"
                            ></tooltip>
                          </span>
                          <input
                            type="number"
                            id="saving-account"
                            name="saving-account"
                            :value="paymentInfo.savingAccount"
                            @input="onSavingAcctChange"
                            @blur="savingAccountTouched = true"
                            class="kite-form-control"
                            :class="{'kite-form-control-danger': !saveAccountValid}"
                          >
                          <span
                            class="danger-message"
                            :class="{show: !saveAccountValid}"
                          >
                            Please enter a valid account number.
                          </span>
                        </label>
                      </div>
                    </div>
                    <!-- // end savings // -->

                  </transition>

                </div>
              </div> <!-- end .payment-info -->

            </div>
            <!-- // end .auto-pay -->

            <div class="button-row">

              <label
                v-if="(billStatus !== 'none') && (billStatus !== 'pastdue')"
                for="skip-payment"
                class="kite-custom-control kite-custom-checkbox"
              >
                <input
                  type="checkbox"
                  name="skip-payment"
                  id="skip-payment"
                  class="kite-custom-control-input"
                  :value="skipFirstPayment"
                  @change="onSkipPaymentToggle"
                >
                <div class="kite-custom-control-indicator"></div>
                <span class="kite-custom-control-description">
                  <span class="label">Already Paid Amount Due</span>
                  <tooltip
                    :tip="skipPaymentTip"
                    :isLight="true"
                  />
                </span>
              </label>
              
              <p class="small">
                By <span v-if="(billStatus === 'pastdue') || (billStatus === 'balance-cutoff')">making this payment and</span> enrolling in Auto Pay, you are confirming your agreement to the <a href="//www.spectrum.net" target="_blank">Terms &amp; Conditions</a>.
              </p>


              <spinner-button
                :after-spin="saveAndNext"
                :btn-copy="submitCopy"
                :disabled="!canContinue"
              ></spinner-button>

              <button
                type="button"
                class="kite-btn kite-btn-link kite-btn-standalone-link"
                @click="onSkip"
              >
                Skip
              </button>
            </div>


            <!-- For Dev -->
            <div class="dev-setting-group">
              <label
                for="balance-type"
                class="input-group"
              >
                <span class="label">Choose account status</span>
                <select
                  name="balance-type"
                  id="balance-type"
                  :value="billStatus"
                  @change="onBillStatusChange"
                >
                  <option value="none">No Balance</option>
                  <option value="balance">Balance Ready</option>
                  <option value="pastdue">Account Past Due</option>
                  <option value="balance-cutoff">Balance Ready - Tomorrow</option>
                </select>
              </label>

              <button
                class="kite-btn kite-btn-sm kite-btn-quick-link error-btn"
                type="button"
                @click="showError"
              >
                Show Error
              </button>
            </div>
            <!-- End 'for Dev' -->

          </div>
        </section>

      </div>
    </div>

  </div>


</template>



<script>

  import CleaveInput from '../inputs/cleave-input.vue';
  import SpinnerButton from '../inputs/spinner-button.vue';
  import Tooltip from '../tooltip/tooltip.vue';
  import Loader from '../svg/loader.vue';

  import { mapGetters, mapState } from 'vuex';

  const accountTipImg = require('../../assets/img/check-account-number.png');
  const routingTipImg = require('../../assets/img/check-routing-number.png');


  const creditCardImgMap = {
    uatp: `/assets/cards/other.png`,
    amex: `/assets/cards/amex.png`,
    discover: `/assets/cards/discover.png`,
    diners: `/assets/cards/diners.png`,
    mastercard: `/assets/cards/mastercard.png`,
    dankort: `/assets/cards/other.png`,
    instapayment: `/assets/cards/other.png`,
    jcb: `/assets/cards/jcb.png`,
    maestro: `/assets/cards/maestro.png`,
    visa: `/assets/cards/visa.png`,
  };


  export default {
    name: 'slide-billing-autopay',
    components: {
      CleaveInput,
      SpinnerButton,
      Tooltip,
      Loader,
    },
    props: [],
    data: function() {
      return {
        classes: 'slide-billing',
        creditCardType: '',

        // validation properties
        cardTouched: false,
        cardExpirationTouched: false,
        checkAccountTouched: false,
        checkRoutingTouched: false,
        savingAccountTouched: false,
        savingRoutingTouched: false,

        showLineItemLoader: false,

        accountTip: `<img class="img-check-tip" src="${accountTipImg}" alt="account number location on check">`,
        routingTip: `<img class="img-check-tip" src="${routingTipImg}" alt="routing number location on check">`,

        skipPaymentTip: 'If you already paid your amount due of $193.10 by other means, such as sending a check, well schedule your first Auto Pay payment for your next billing cycle.<br/><br/>To avoid any late fees, please ensure you\'ve paid all outstanding charges.'
      };
    },
    computed: {
      creditCardAsset: function() {
        if (this.paymentInfo.creditCardType) {
          return creditCardImgMap[this.paymentInfo.creditCardType];
        } else {
          return null;
        }
      },
      canContinue: function() {

        let { paymentMethod } = this.paymentInfo;

        switch (paymentMethod) {
          case ('credit'):
            return (
              (this.cardValid && this.cardExpireValid) &&
              (this.paymentInfo.card != '' && this.paymentInfo.cardExpiration != '')
            );
            break;
          case ('checking'):
            return (
              (this.checkAccountValid && this.checkRoutingValid) &&
              (this.paymentInfo.checkAccount != '') && 
              (this.paymentInfo.checkRouting != '')
            );
            break;
          case ('savings'):
            return (
              (this.saveAccountValid && this.saveRoutingValid) &&
              (this.paymentInfo.savingAccount != '' && this.paymentInfo.savingRouting != '')
            );
            break;
        }

      },

      // Micro-copy based on config
      extraCopy: function() {
        switch (this.billStatus) {
          case ('none'):
          case ('balance'):
            return ``;
            break;
          case ('balance-cutoff'): // The 24hr before the payment is due
            return `
              <p class="kite-lead">Your first payment of $123.45 will be paid today with your Auto Pay enrollment.</p>
            `;
            break;
          case ('pastdue'):
            return `
              <p class="kite-lead">Your balance includes a past due amount of $49.99. After you enroll, your total balance of $123.45 will be paid today with your Auto Pay enrollment.</p>
            `;
            break;
        }
      },

      submitCopy: function() {
        switch (this.billStatus) {
          case ('none'):
          case ('balance'):
            return `Enroll in Auto Pay`;
            break;
          case ('balance-cutoff'): // The 24hr before the payment is due
          case ('pastdue'):
            return `Review Payment & Enroll`;
            break;
        }
      },

      // Error states
      isCardTypeError: function() {
        return !(
          this.paymentInfo.creditCardType === 'amex' ||
          this.paymentInfo.creditCardType === 'visa' ||
          this.paymentInfo.creditCardType === 'mastercard' ||
          this.paymentInfo.creditCardType === 'discover'
        );
      },
      cardValid: function() {
        return (
          (
            (this.paymentInfo.paymentMethod === 'credit') && 
            (this.paymentInfo.card != '') &&
            (!this.isCardTypeError && this.cardTouched)
          ) || !this.cardTouched
        );
      },
      cardExpireValid: function() {
        return (
          (
            (this.paymentInfo.paymentMethod === 'credit') && 
            !(this.paymentInfo.cardExpiration === '')
          ) || !this.cardExpirationTouched
        );
      },

      checkAccountValid: function() {
        return (
          (
            (this.paymentInfo.paymentMethod === 'checking') && 
            (
              this.paymentInfo.checkAccount != '' && 
              this.paymentInfo.checkAccount.length > 3 &&
              this.paymentInfo.checkAccount.length < 18
            )
          ) || !this.checkAccountTouched
        );
      },
      checkRoutingValid: function() {
        return (
          (
            (this.paymentInfo.paymentMethod === 'checking') && 
            (
              this.paymentInfo.checkRouting != '' &&
              this.paymentInfo.checkRouting.length === 9
            )
          ) || !this.checkRoutingTouched
        );
      },

      saveAccountValid: function() {
        return (
          (
            (this.paymentInfo.paymentMethod === 'savings') && 
            (this.paymentInfo.savingAccount != '')
          ) || !this.savingAccountTouched
        );
      },
      saveRoutingValid: function() {
        return (
          (
            (this.paymentInfo.paymentMethod === 'savings') && 
            (
              this.paymentInfo.savingRouting != '' &&
              this.paymentInfo.savingRouting.length === 9
            )
          ) || !this.savingRoutingTouched
        );
      },

      ...mapGetters([
        'currentSlide',
        'isMobile',
        'today',
        'paymentDate',
        'nextPaymentDate',
        'skipPaymentNextDate',
      ]),
      ...mapState([
        'billStatus',
        'skipFirstPayment',
        'testingVersion',
        'paymentInfo',
      ]),
    },
    methods: {

      // Navigation
      //--------
      saveAndNext: function() {
        if (this.billStatus === 'none' || this.billStatus === 'balance' ) {
          this.$store.commit('showPaymentConfirmModal');
        } else {
          this.$store.commit('showPaymentReviewModal');
        }
      },
      onSkip: function() {
        if (this.canContinue) {
          this.$store.commit('showPaymentSpeedbumpModal');
        } else {
          this.nextSlide();
        }
      },
      nextSlide: function() {
        this.$router.push(`/app/${this.currentSlide+1}`);
      },


      // Payment Info
      //--------------
      onPaymentMethodChange: function(e) {
        // Move underline to selected tab
        this.positionUnderline(e.target);

        this.$store.commit('setPaymentMethod', e.target.value);
      },
      positionUnderline: function(el) {
        // el should be one of the tab checkbox elements
        this.underlineEl.style.left = el.offsetParent.offsetLeft + 'px';
        this.underlineEl.style.width = el.offsetWidth + 'px';
      },
      onPaymentCardValueChange: function(val) {
        this.$store.commit('setPaymentCardValue', val);
        this.cardTouched = true;
      },
      onPaymentCardExpChange: function(val) {
        this.$store.commit('setPaymentCardExp', val);
        this.cardExpirationTouched = true;
      },
      onPaymentCardTypeChange: function(type) {
        this.$store.commit('setPaymentCardType', (type === 'unknown') ? '' : type);
      },
      onCheckingRouteChange: function(e) {
        this.$store.commit('setCheckingRoute', e.target.value);
      },
      onCheckingAcctChange: function(e) {
        this.$store.commit('setCheckingAcct', e.target.value);
      },
      onSavingRouteChange: function(e) {
        this.$store.commit('setSavingRoute', e.target.value);
      },
      onSavingAcctChange: function(e) {
        this.$store.commit('setSavingAcct', e.target.value);
      },
      resetInfo: function() {
        this.$store.commit('setPaymentCardValue', '');
        this.$store.commit('setPaymentCardExp', '');
        this.$store.commit('setPaymentCardType', 'unknown');
        this.$store.commit('setCheckingRoute', '');
        this.$store.commit('setCheckingAcct', '');
        this.$store.commit('setSavingRoute', '');
        this.$store.commit('setSavingAcct', '');

        this.cardTouched = false;
        this.cardExpirationTouched = false;
        this.checkAccountTouched = false;
        this.checkRoutingTouched = false;
        this.savingAccountTouched = false;
        this.savingRoutingTouched = false;
      },


      // Other Stuff
      //------------
      onSkipPaymentToggle: function(e) {
        // show loader
        this.showLineItemLoader = true;
        console.log('one');
        
        // wait
        window.setTimeout(() => {
        console.log('two');
          this.$store.commit('setSkipFirstPayment', e.target.checked);
          this.showLineItemLoader = false;
        }, 1200);
      },


      // Dev
      //---------
      onBillStatusChange: function(e) {
        this.$store.commit('setBillStatus', e.target.value);
      },
      showError: function(e) {
        this.$store.commit('showPaymentErrorModal');
      },
    },
    mounted: function() {
      this.underlineEl = this.$el.querySelector('.selected-underline');

      // set the underline width
      this.positionUnderline(this.$el.querySelector('#autopay-checking'));
    },
  };


</script>



<style lang="scss">

