@import "~@kite/shared/mixins";

// @include kite-media-breakpoint-up(md) { }


.slide-billing {

  h4 {
    margin-bottom: 30px;
  }
  .section-payment {
    .kite-lead {
      margin-bottom: 18px;
    }
    .download-link {
      svg {
        display: inline-block;
        vertical-align: bottom;
        margin-right: 4px;
      }
    }
    .auto-pay {
      margin: 0;
    }

    // Auto Pay Info Box
    //-----------
    .payment-info-box {
      position: relative;
      margin: 24px 0 40px;
      padding: 16px;
      border: 1px solid $kite-gray-2;
      border-radius: 4px;

      .loader-overlay {
        opacity: 0;
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        background-color: rgba(255,255,255, 0.8);
        transition: all 0.4s;
        pointer-events: none;

        .loader-position {
          display: inline-block;
          width: auto;
          height: auto;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        }

        &.show {
          opacity: 1;
        }
      }
      .title-row {
        margin-bottom: 16px;

        h4 {
          display: inline-block;
          width: auto;
          margin: 0;
        }
        a {
          position: relative;
          top: 1px;
          display: inline-block;
          width: auto;
          float: right;
          padding: 2px 0;
          font-size: 1rem;
          font-weight: 700;
          color: $kite-blue-2;
        }
      }
      .line-item {
        display: flex;
        align-items: center;
        margin-bottom: 14px;

        &:last-child {
          margin-bottom: 0;
