<script>

  import {TimelineMax, TweenLite} from 'gsap';


  export default {
    name: 'svg-computer',
    components: {
    },
    props: [
      'showing',
    ],
    data: function() {
      return {
        tl: null,
      };
    },
    watch: {},
    methods: {
      initTimeline: function() {
        const footballEl = this.$el.querySelector('#football');

        this.tl.to(footballEl, 0, {x:-35, y:30}, 0)
          .to(footballEl, 12, {bezier: {type: 'quadratic', values: [{x:-35, y:40}, {x:25, y:-70} ,{x:130, y:30}]}, rotation:-1280, ease: Power1.easeOut }, 0);
      },
      restart() {
        this.tl.restart();
      }
    },
    mounted: function() {
      // init the timeline
      this.tl = new TimelineMax({repeat: -1, repeatDelay: 2});
      this.initTimeline();
    }
  };

</script>



<style lang="scss">

@import "~@kite/shared/variables";
@import "~@kite/shared/mixins";


@keyframes footballPhoneSlide {
  0% {
    transform: translateX(30px);
  }
  100% {
    transform: translateX(0);
  }
}


.devices-football-container {

  svg {
    display: inline-block;
    width: 100%;
    height: auto;

    #football {
      position: relative;
      transform-origin: 3px 3px;
    }
  }

  @include kite-media-breakpoint-up(md) {
    svg {
      #devices-phone {
        transform: translateX(20px);
        transition: transform 1.5s;
      }
      #devices-remote {
        transform-origin: 50% 100%;
        transform: translateY(-2px) rotate(20deg);
        transition: transform 1s 0.5s ease-in;
      }
      #devices-ipad {
        transform: translateX(-25px);
        transition: transform 1.5s;
      }
      #devices-tv-content {
        opacity: 0;
        transition: opacity 0.2s 1.5s;
      }
    }

    &.showing {
      svg {
        #devices-phone {
          transform: translateX(0);
        }
        #devices-remote {
          transform: translateY(0) rotate(0);
        }
        #devices-ipad {
          transform: translateX(0);
        }
        #devices-tv-content {
          opacity: 1;
        }
      }
    }
  }
}

</style>