<script>

  import ApptDateSlider from '../slider/appt-date-slider.vue';
  import ModalBase from './modal-base.vue';
  import Tooltip from '../tooltip/tooltip.vue';
  import { mapState, mapGetters } from 'vuex';


  const stepElMap = {
    1: '.appt-calendar',
    2: '.appt-time',
    3: '.appt-confirm'
  };


  export default {
    name: 'appt-modal',
    components: {
      ApptDateSlider,
    },
    props: [
    ],
    data: function() {
      return {
        apptStep: 1,
        viewHeight: 0,
      };
    },
    methods: {
      hideModal: function() {
        this.$store.commit('hideApptModal');

        window.setTimeout(() => {
          this.apptStep = 1;
        }, 1000);
      },
      nextStep: function() {
        this.apptStep += 1;
      },
      prevStep: function() {
        this.apptStep -= 1;
      },

      setViewHeight: function() {
        const nextEl = this.$el.querySelector(stepElMap[this.apptStep]);
        this.viewHeight = nextEl.scrollHeight;
      },
      viewEnter: function() {
        this.setViewHeight();
      }
    },
    computed: {
      ...mapState([
        'canShowApptModal',
      ]),
    },
    mounted: function() {
      this.setViewHeight();
    },
  };

</script>



