<template>

  <modal-base
    class-name="payment-confirm-modal"
    :can-show="canShowPaymentConfirmModal"
    :hide="onDone"
    cta-copy="Continue"
    :cta-action="onDone"
  >
    <div slot="content" class="payment-confirm-modal-content">
      <div class="img-top-icon">
        <img src="../../assets/img/icon-check-line.svg" alt="Check mark icon">
      </div>

      <template v-if="(billStatus === 'none') || (billStatus === 'balance')">
        <h2>Thanks for Enrolling in Auto Pay</h2>
        <p>You'll receive a confirmation message shortly. It may take up to 24 hours for payments to post to your account.</p>
      </template>

      <template v-if="(billStatus === 'pastdue') || (billStatus === 'balance-cutoff')">
        <h2>Thanks for Your Payment &amp; Auto Pay&nbsp;Enrollment</h2>
        <p>Your payment of $123.45 is being processed. You'll receive a confirmation message shortly. It may take up to 24 hours for payments to post to your account.</p>
      </template>


      <div class="details-table">
        <div
          v-if="(billStatus === 'pastdue')"
          class="detail-row"
        >
          <span class="left">Confirmation #</span>
          <span class="right">12345678910</span>
        </div>
        <div
          v-if="(billStatus === 'pastdue')"
          class="detail-row"
        >
          <span class="left">Payment Amount</span>
          <span class="right">$123.45</span>
        </div>
        <div class="detail-row">
          <span class="left">First Payment Date</span>
          <span class="right">
            <span v-if="billStatus === 'pastdue' || billStatus === 'balance-cutoff'">{{today}}</span>
            <span v-else-if="skipFirstPayment">On or around {{nextPaymentDate}}</span>
            <span v-else>{{paymentDate}}</span>
          </span>
        </div>
        <div class="detail-row">
          <span class="left">Payment Method</span>
          <span class="right">
            <!-- <img src="../../assets/img/card-visa-blue.svg" alt="Visa card logo icon"> -->
            <template v-if="paymentInfo.paymentMethod === 'credit'">
              <img
                :src="require('../../assets/img/card-visa-blue.svg')"
                v-if="paymentInfo.creditCardType === 'visa'"
                alt="visa"
              >
              <img
                :src="require('../../assets/img/card-mastercard-blue.svg')"
                v-if="paymentInfo.creditCardType === 'mastercard'"
                alt="mastercard"
              >
              <img
                :src="require('../../assets/img/card-discover-blue.svg')"
                v-if="paymentInfo.creditCardType === 'discover'"
                alt="discover card"
              >
              <img
                :src="require('../../assets/img/card-amex-blue.svg')"
                v-if="paymentInfo.creditCardType === 'amex'"
                alt="american express"
              >
            </template>
            <span v-if="paymentInfo.paymentMethod === 'checking'">Checking</span>
            <span v-if="paymentInfo.paymentMethod === 'savings'">Savings</span>
            {{paymentDigits}}
          </span>
        </div>
        <div class="detail-row">
          <span class="left">Next Payment Date</span>
          <span class="right">
            <span v-if="billStatus === 'pastdue' || billStatus === 'balance-cutoff'">{{paymentDate}}</span>
            <span v-else-if="skipFirstPayment">On or around {{skipPaymentNextDate}}</span>
            <span v-else>On or around {{nextPaymentDate}}</span>
            <tooltip
              :tip="futureTooltip"
              :isLight="true"
            />
          </span>
        </div>
      </div>
    </div>
  </modal-base>

</template>



<script>

  import ModalBase from './modal-base.vue';
  import Tooltip from '../tooltip/tooltip.vue';
  import { mapState, mapGetters } from 'vuex';


  export default {
    name: 'payment-confirm-modal',
    components: {
      ModalBase,
      Tooltip,
    },
    props: [
    ],
    data: function() {
      return {
        futureTooltip: 'Payments will be processed 20 to 24 days from the start of your billing cycle. You can find your billing cycle date on your statement.',
      };
    },
    methods: {
      onDone: function() {
        this.$store.commit('hidePaymentConfirmModal');
        this.$router.push(`/app/${this.currentSlide+1}`);
      }
    },
    computed: {
      ...mapState([
        'canShowPaymentConfirmModal',
        'billStatus',
        'skipFirstPayment',
        'paymentInfo',
      ]),
      ...mapGetters([
        'currentSlide',
        'today',
        'paymentDate',
        'nextPaymentDate',
        'skipPaymentNextDate',
      ]),
      paymentDigits: function() {
        const {paymentInfo} = this;
        let accountNumber;

        switch (paymentInfo.paymentMethod) {
          case ('checking') :
            accountNumber = paymentInfo.checkAccount;
            break;
          case ('savings') :
            accountNumber = paymentInfo.savingAccount;
            break;
          case ('credit') :
            accountNumber = paymentInfo.card;
            break;
        };

        if (accountNumber) {
          return accountNumber.substr(-4);
        } else {
          return '';
        }
      },
    }
  };

</script>



