    margin-top: 48px;
    margin-bottom: 48px;

    .detail-row {
      display: flex;
      margin-bottom: 16px;

      .left,
      .right {
        display: inline-block;
        flex: 1;
        font-weight: 700;
        color: $kite-black;
      }
      .left {
        text-align: left;
      }
      .right {
        text-align: right;

        img {
          position: relative;
          top: -1px;
          display: inline-block;
          height: 16px;
          margin-right: 10px;
        }
      }
      .tooltip {
        svg {
          vertical-align: top;
          top: 4px;
        }
      }
    } // end .detail-row
  } // end .details-table

}

</style>
