
<template>

  <div class="component-sign-in">

    <div class="logo">
      <svg-start-logo
        fill="#fff"
      ></svg-start-logo>
    </div>

    <div class="center-me show">

      <h2>Sign In to Get Started</h2>

      <form id="form-sign-in">
        <label for="name" class="input-group">
          <span class="label">
            Username
            <tooltip
              :tip="usernameTip"
            ></tooltip>
          </span>
          <input 
            type="text"
            id="name"
            name="name"
            v-model="username"
            class="kite-form-control"
          >
        </label>
        <label for="name" class="input-group">
          <span class="label">Password</span>
          <input 
            type="password"
            id="password"
            name="password"
            v-model="password"
            class="kite-form-control"
          >
        </label>

        <a href="" class="forgot-link">Forgot Username or Password?</a>
      </form>

      <div class="action-container">
        <div class="action-item">
          <spinner-button
            btnCopy="Sign In"
            :afterSpin="onSubmit"
            :disabled="(password === '' || username === '')"
          ></spinner-button>
        </div>
        
        <!-- <div class="action-item">
          <button
            class="kite-btn kite-btn-link kite-btn-quick-link"
            @click="onSubmit"
          >
            Skip
          </button>
        </div> -->
      </div>

    </div>
  </div>

</template>



<script>

  import SpinnerButton from '../inputs/spinner-button.vue';
  import SvgStartLogo from '../header/logo.vue';
  import Tooltip from '../tooltip/tooltip.vue';

  import { mapState } from 'vuex';


  export default {
    name: 'sign-in',
    components: {
      SpinnerButton,
      SvgStartLogo,
      Tooltip,
    },
    props: [],
    data: function() {
      return {
        username: '',
        password: '',
        newStart: 0,
        usernameTip: 'To create your username, visit the Spectrum Start link in your order confirmation email or create one on <a href="//www.spectrum.net" target="_blank">Spectrum.net</a>.',
      };
    },
    computed: {
      ...mapState([
        'startingPoint',
        'startIsExpired',
      ])
    },
    methods: {
      onSubmit: function() {
        if (this.startIsExpired) {
          this.$router.push('/expired');
        } else {
          this.$router.push(`app/${this.startingPoint || 3}`);
        }

      },
    },
  };

</script>



