
<template>

  <div class="watchlist-tile">

    <div
      class="inline-tile"
      :class="{submitted: (savedBefore && !!savedCount)}"
      @click="showOverlay"
    >
      <img :src="iconUrl" alt="icon">
      <div class="tile-title">{{title}}</div>
      
      <div class="img-overlay">
        <div class="check-bg">
          <span class="selected-count">{{savedCount}}</span>
        </div>
      </div>
    </div>

    <div 
      class="watchlist-overlay"
      :class="{
        show: showingOverlay,
        hiding: hidingOverlay,
      }"
    >
      <div class="overlay-content">
        <h3 class="overlay-title">
          {{title}}
        </h3>
        <div
          class="overlay-show-list"
          :class="{
            success: showSuccess
          }"
        >
          <slider>

            <div
              v-for="show in list"
              class="img"
              :class="{tall: show.movie, selected: show.selected}"
              :style="{backgroundImage: `url(${show.url})`}"
              @click="selectShow(show)"
            >
              <div class="img-overlay">
                <div class="show-title">{{show.name || 'Show Name'}}</div>
                <div class="corner"></div>
                <svg class="check" width="24" height="24" viewBox="0 0 18 18" xmlns="http://www.w3.org/2000/svg">
                  <path stroke="#FFF" fill="none" fill-rule="evenodd" stroke-width="2" d="M3.682 9.41l2.863 2.863 6.546-6.546"/>
                </svg>
              </div>
            </div>

          </slider>

          <div
            class="show-list-success"
            :class="{
              show: showSuccess
            }"
          >
            <div class="center-me">
              <svg width="18" height="18" viewBox="0 0 18 18" xmlns="http://www.w3.org/2000/svg">
                <path d="M9 18A9 9 0 1 1 9 0a9 9 0 0 1 0 18zm3.384-12.98l-5.839 5.839-2.156-2.156-1.414 1.414 3.57 3.57 7.253-7.253-1.414-1.414z" fill="#00BF1F" fill-rule="nonzero"/>
              </svg>
              <h3 class="success-title">Added to My Library</h3>
              <div class="success-msg">To start watching your selections, visit My Library on the Spectrum TV App. You can always edit your selections later on the app.</div>
            </div>
          </div>
        </div>

        <transition name="fade" mode="out-in">
          <div
            v-if="!showSuccess"
            key="choice"
            class="overlay-actions"
          >
            <spinner-button
              :afterSpin="afterSubmit"
              :btnCopy="submitBtnCopy"
              :disabled="selectedCount === savedCount"
            ></spinner-button>
            <button
              class="kite-btn kite-btn-link kite-btn-standalone-link"
              @click="hideOverlay"
            >
              Cancel
            </button>
          </div>

          <div
            v-else
            key="success"
            class="overlay-actions"
          >
            <button
              class="kite-btn kite-btn-lg kite-btn-primary"
              @click="hideOverlay"
            >
              OK
            </button>
          </div>
        </transition>

      </div>

      <div 
        class="close"
        @click="hideOverlay"
      >
        <svg width="19" height="19" viewBox="0 0 19 19" xmlns="http://www.w3.org/2000/svg">
          <g stroke="#fff" stroke-width="3" fill="none" fill-rule="evenodd" stroke-linecap="square">
            <path d="M2.5 16.5l14-14M2.5 2.5l14 14"/>
          </g>
        </svg>
      </div>

    </div>

  </div>


</template>



<script>

  import { mapState, mapGetters } from 'vuex';
  import SpinnerButton from '../inputs/spinner-button.vue';
  import Slider from '../grid-slider/slider.vue';


  export default {
    name: 'watchlist-tile',
    components: {
      SpinnerButton,
      Slider,
    },
    props: [
      'title',
      'iconUrl',
      'list',
    ],
    data: function() {
      return {
        showingOverlay: false,
        hidingOverlay: false,
        overlayEl: null,
        showSuccess: false,

        savedBefore: false,
        savedCount: 0,
      };
    },
    computed: {
      selectedCount: function() {
        let count = 0;
        this.list.forEach((show) => {
          if (show.selected) {
            count++;
          }
        });
        return count;
      },
      submitBtnCopy: function() {
        if (this.savedBefore) {
          return 'Update Items';

        } else if (!this.selectedCount) {
          return 'Select Channels & Titles';

        } else {
          return (this.selectedCount > 1) ? `Add ${this.selectedCount} Items` : `Add ${this.selectedCount} Item`
        }
      },
      ...mapGetters([
        'showingWatchlistOverlay',
        'isMobile'
      ]),
    },
    methods: {
      getTitlePosition: function() {
        return {
          topPos: this.$el.offsetTop,
          leftPos: this.$el.offsetLeft,
          width: this.$el.clientWidth,
          height: this.$el.clientHeight
        };
      },
      positionOverlay: function(positionObj) {
        this.overlayEl.setAttribute('style', `top:${positionObj.topPos}px; left:${positionObj.leftPos}px; height:${positionObj.height}px; width:${positionObj.width}px;`);
      },
      toggleBodyScroll: function() {
        this.bodyEl.classList.toggle('no-scroll');
      },

      showOverlay: function() {
        // if mobile, fix scroll
        if (this.isMobile) {
          this.toggleBodyScroll();
        }

        // get position and dimensions of tile
        const startPos = this.getTitlePosition();

        // position overlay on top, and style
        this.positionOverlay(startPos);
        this.showingOverlay = true;
        this.$store.commit('setShowingWatchlistOverlay', true);

        // animate the overlay to full screen
        this.$nextTick(() => {
          this.overlayEl.setAttribute('style', 'top:0; left:0; height:100%; width:100%;  opacity:1;');
        });
      },

      hideOverlay: function() {
        const endPos = this.getTitlePosition();

        this.hidingOverlay = true;
        this.positionOverlay(endPos);

        // if mobile, fix scroll
        if (this.isMobile) {
          this.toggleBodyScroll();
        }

        window.setTimeout(() => {
          this.showingOverlay = false;
          this.hidingOverlay = false;
          this.showSuccess = false;
          this.$store.commit('setShowingWatchlistOverlay', false);
        }, 1000);
      },

      selectShow: function(show) {
        show.selected = !show.selected;
      },
      afterSubmit: function() {
        this.showSuccess = true;
        this.savedBefore = true;
        this.savedCount = JSON.parse(JSON.stringify(this.selectedCount));
      },
    },
    mounted: function() {
      this.overlayEl = this.$el.querySelector('.watchlist-overlay');
      this.bodyEl = document.querySelector('#body');
    }

  };

</script>



