
<template>

  <modal-base
    title="Still There?"
    :class-name="modalClass"
    :can-show="canShowTimeoutModal"
    :hide="hideModal"
    cta-copy="Still Here"
    :cta-action="hideModal"
  >
    <div slot="content" class="timeout-modal-content">
      We noticed you haven't done anything in a while. You can continue setting up and learning about your service. Otherwise, your session will time out in two minutes.
    </div>
  </modal-base>

</template>



<script>

  import ModalBase from './modal-base.vue';
  import { mapState } from 'vuex';


  export default {
    name: 'timeout-modal',
    components: {
      ModalBase,
    },
    props: [
      'canShow',
      'hide',
      'cta',
      'secondCta',
    ],
    data: function() {
      return {
        modalClass: 'timeout-modal',
      };
    },
    computed: {
      ...mapState([
        'canShowTimeoutModal',
      ]),
    },
    methods: {
      hideModal: function() {
        this.$store.commit('hideTimeoutModal');
      }
    }
  };

</script>



