
<template>

  <div class="slide-account">

    <div class="slide-content-wrapper kite-container">
      <div class="content">

          <section class="section-credentials">
            <div class="section-content">
              <h1>Choose a Security Question</h1>
              <p class="kite-lead">To finish creating your username <strong>({{ userInfo.username }})</strong>, choose a security question.</p>


              <form class="password-form">

                <label for="security-question">
                  <span class="label">Security Question</span>
                  <select
                    name="security-question"
                    id="security-question"
                    v-model="securityQuestion"
                    class="kite-form-control"
                    autocomplete="off"
                  >
                    <option value=""></option>
                    <option value="teacher">What is your mother's maiden name?</option>
                    <option value="pet">What was the make and model of your first car?</option>
                    <option value="sports">What was the name of your first pet?</option>
                    <option value="first-car">What is the name of the street where you grew up?</option>
                    <option value="movie">In what city were you born?</option>
                  </select>
                </label>

                <label for="security-answer">
                  <span class="label">Security Question Answer</span>
                  <input
                    type="text"
                    id="security-answer"
                    v-model="securityAnswer"
                    class="kite-form-control"
                    :class="{
                    }"
                    autocomplete="off"
                  >
                </label>

                <div class="small">By creating a username, you agree to Spectrum's <a href="https://www.spectrum.com/policies/residential-terms.html" target="_blank">Terms &amp; Conditions</a>.</div>

                <toast-button
                  btn-copy="Continue"
                  :disabled="!enableSubmit"
                  :after-spin="nextSlide"
                  :show-toast="showSuccessMessage"
                ></toast-button>
              </form>


            </div>
          </section>

      </div>
    </div>

  </div>

</template>



<script>

  import ToastButton from '../toast/toast-button.vue';
  import { mapState, mapGetters } from 'vuex';


  export default {
    name: 'slide-account-password',
    components: {
      ToastButton,
    },
    props: [],
    data: function() {
      return {
        securityAnswer: '',
        securityQuestion: '',
      };
    },
    computed: {
      enableSubmit: function() {
        return (
          !!this.securityAnswer &&
          !!this.securityQuestion
        );
      },
      ...mapState([
        'userInfo',
      ]),
      ...mapGetters([
        'currentSlide',
      ]),
    },
    methods: {
      showSuccessMessage: function() {
        this.$store.commit('showToast', 'Username Created');
      },
      nextSlide: function() {
        this.$router.push(`/app/${this.currentSlide+1}`);
      }
    },

  };

</script>



