
<template>

  <div class="appt-date-slider">
    <div
      class="prev-day arrow"
      @click="prevDay"
    >
      <img src="../../assets/img/icon-chevron-right-blue.svg" alt="left">
    </div>
    <div class="slider-window">
      <div
        class="slider-track"
        :style="`transform: translateX(-${trackOffset}px)`"
      >
        <div class="date">
          <div class="weekday">Fri</div>
          <div class="number">29</div>
        </div>
        <div class="date">
          <div class="weekday">Sat</div>
          <div class="number">30</div>
        </div>
        <div class="date unavailable">
          <div class="weekday">Sun</div>
          <div class="number">31</div>
        </div>
        <div class="date">
          <div class="weekday">Mon</div>
          <div class="number">1</div>
        </div>
        <div class="date selected">
          <div class="weekday">Tues</div>
          <div class="number">2</div>
        </div>
        <div class="date unavailable">
          <div class="weekday">Wed</div>
          <div class="number">3</div>
        </div>
        <div class="date">
          <div class="weekday">Thur</div>
          <div class="number">4</div>
        </div>
        <div class="date">
          <div class="weekday">Fri</div>
          <div class="number">5</div>
        </div>
        <div class="date">
          <div class="weekday">Sat</div>
          <div class="number">6</div>
        </div>
        <div class="date unavailable">
          <div class="weekday">Sun</div>
          <div class="number">7</div>
        </div>
      </div>
    </div>
    <div
      class="next-day arrow"
      @click="nextDay"
    >
      <img src="../../assets/img/icon-chevron-right-blue.svg" alt="left">
    </div>
    <div class="selected-underline"></div>
  </div>

</template>



<script>

  export default {
    name: 'appt-date-slider',
    components: {
    },
    props: [
    ],
    data: function() {
      return {
        trackWidth: 0,
        windowWidth: 0,
        slideDistance: 0,
        trackOffset: 0,
        selectedDateEl: {},
      };
    },
    methods: {
      prevDay: function() {
        this.trackOffset -= this.slideDistance;

        this.selectedDateEl.classList.remove('selected');
        this.selectedDateEl = this.selectedDateEl.previousElementSibling;
        this.selectedDateEl.classList.add('selected');
      },
      nextDay: function() {
        this.trackOffset += this.slideDistance

        this.selectedDateEl.classList.remove('selected');
        this.selectedDateEl = this.selectedDateEl.nextElementSibling;
        this.selectedDateEl.classList.add('selected');
      },
    },
    mounted: function() {
      const trackEl = this.$el.querySelector('.slider-track');
      const windowEl = this.$el.querySelector('.slider-window');

      // set track width, based on number of children and their width
      this.trackWidth = trackEl.offsetWidth;
      this.windowWidth = windowEl.offsetWidth;

      // center on selected date
      this.selectedDateEl = trackEl.querySelector('.selected');
      this.slideDistance = this.selectedDateEl.offsetWidth;
      this.trackOffset = this.selectedDateEl.offsetLeft - (this.windowWidth/2) + (this.slideDistance/2);
    }
  };

</script>



<style lang="scss">

