
<template>

  <div class="slide-account-verify">


    <div class="slide-content-wrapper kite-container">
      <div class="content">

          <section class="section-account">
            <div class="section-content">
              <h1>Check Your&nbsp;Info</h1>
              <p class="kite-lead details-title">Here's what we know about you so far. We'll use this info to create your account online, so please make sure everything looks good.</p>
              <p class="kite-lead">After you complete setup, you can always edit your account info later at Spectrum.net.</p>


              <!-- Grey info box -->
              <div class="uneditable-info-box">
                <div class="info name">
                  <div class="title">Name</div>
                  <div class="data">{{userInfo.name}}</div>
                </div>
                <div class="info">
                  <div class="title">Service Address</div>
                  <div class="data">{{userInfo.street}}</div>
                  <div class="data">{{`${userInfo.city}, ${userInfo.state}  ${userInfo.zip}`}}</div>

                  <!-- Checkbox - Same address -->
                  <label class="kite-custom-control kite-custom-checkbox input-group same-checkbox" for="same-address">
                    <span class="kite-custom-control-description">
                      Same as Billing Address
                    </span>
                    <input
                      id="same-address"
                      name="same-address"
                      type="checkbox"
                      class="kite-custom-control-input"
                      :checked="useSameAddress"
                      @change="onSameAddressChange"
                    />
                    <span class="kite-custom-control-indicator"></span>
                  </label>
                </div>
              </div>


              <form
                id="verify-account"
              >

              <!-- Edit/Cancel Buttons -->
                <div class="toggle-btn-container">
                  <div
                    class="non-editing-actions"
                    :class="{show: !editing}"
                  >
                    <button
                      class="kite-btn kite-btn-link"
                      type="button"
                      @click="startEdit"
                    >
                      <svg width="16" height="16" viewBox="0 0 16 16" xmlns="http://www.w3.org/2000/svg">
                        <g id="SS_Desk_Confirm_Info_4" fill="#0073D1">
                          <path d="M11.682 0L1.038 10.672 0 16l5.357-.998L16 4.33 11.682 0zM2.874 14L2 13.12l.442-2.115.005-.005L5 13.57 2.874 14z" id="ic_edit"/>
                        </g>
                      </svg>
                      EDIT
                    </button>
                  </div>
                  <div
                    class="editing-actions"
                    :class="{show: editing}"
                  >
                    <button
                      class="kite-btn kite-btn-link"
                      type="button"
                      @click="resetData"
                    >
                      CANCEL
                    </button>
                    <span class="pipe"></span>
                    <button
                      class="kite-btn kite-btn-link"
                      type="button"
                      @click="saveEdit"
                      :disabled="!hasAllFields"
                    >
                      SAVE
                    </button>
                  </div>
                </div>

              <!-- Info Fields -->
                <div
                  class="input-group billing-group"
                  :class="{show: !useSameAddress}"
                >
                  <label for="street" class="no-group">
                    <span class="label">Billing Address</span>
                  </label>
                  <div class="street-address-row">
                    <input 
                      type="text"
                      id="street"
                      name="street"
                      placeholder="Street Address"
                      v-model="user.billingStreet"
                      class="kite-form-control"
                      :class="{
                        'kite-form-control-danger': !user.billingStreet,
                      }"
                      :disabled="!editing"
                    >
                    <input 
                      type="text"
                      id="street2"
                      name="street2"
                      placeholder="Apt. Number, Suite, etc."
                      v-model="user.billingStreet2"
                      class="kite-form-control"
                      :disabled="!editing"
                    >
                  </div>
                  <div class="multi-input-row">
                    <input
                      type="text"
                      id="city"
                      name="city"
                      placeholder="City"
                      v-model="user.billingCity"
                      class="kite-form-control"
                      :class="{
                        'kite-form-control-danger': !user.billingCity,
                      }"
                      :disabled="!editing"
                    >
                    <input 
                      type="text"
                      id="state"
                      name="state"
                      placeholder="State"
                      v-model="user.billingState"
                      class="kite-form-control"
                      :class="{
                        'kite-form-control-danger': !user.billingState,
                      }"
                      :disabled="!editing"
                    >
                    <input 
                      type="text"
                      id="zip"
                      name="zip"
                      placeholder="ZIP Code"
                      v-model="user.billingZip"
                      class="kite-form-control"
                      :class="{
                        'kite-form-control-danger': !user.billingZip,
                      }"
                      :disabled="!editing"
                    >
                  </div>
                  <span
                    class="danger-message"
                    :class="{
                      show: !addressValid,
                    }"
                  >
                    Please enter a valid billing address.
                  </span>
                </div>

                <label for="email" class="input-group">
                  <span class="label">Email Address</span>
                  <input
                    type="email"
                    id="email"
                    name="email"
                    v-model="user.email"
                    class="kite-form-control"
                    :class="{
                      'kite-form-control-danger': !emailValid,
                    }"
                    :disabled="!editing"
                  >
                  <span
                    class="danger-message"
                    :class="{
                      show: !emailValid,
                    }"
                  >
                    Please enter a valid email.
                  </span>
                </label>

                <label for="phone" class="input-group">
                  <span class="label">Phone Number</span>
                  <input
                    type="phone"
                    id="phone"
                    name="phone"
                    v-model="user.phone"
                    @input="updatePhone"
                    class="kite-form-control"
                    :class="{
                      'kite-form-control-danger': !user.phone,
                    }"
                    :disabled="!editing"
                  >
                  <span
                    class="danger-message"
                    :class="{
                      show: !user.phone,
                    }"
                  >
                    Please enter a valid phone number.
                  </span>
                </label>
              </form>
              
              <div class="action-buttons">
                <toast-button
                  :after-spin="nextSlide"
                  btn-copy="Looks Good"
                  :disabled="!enableSubmit"
                  :show-toast="showSuccessMessage"
                ></toast-button>
              </div>
            </div>
          </section>


      </div>
    </div>

  </div>

</template>



<script>

  import ToastButton from '../toast/toast-button.vue';
  import { mapState, mapGetters } from 'vuex';


  export default {
    name: 'slide-account-verify',
    components: {
      ToastButton,
    },
    props: [
      'thisSlide',
    ],
    data: function() {
      return {
        user: {}, // data cloned
        editing: false,
        useSameAddress: true, // TODO: Move to userInfo. Probably create two address fields there too
      };
    },
    computed: {
      enableSubmit: function() {
        return (
          !this.editing && 
          this.hasAllFields
        );
      },
      hasAllFields: function() {
        return (
          !!this.user.street &&
          !!this.user.city &&
          !!this.user.state &&
          !!this.user.zip &&
          this.emailValid &&
          !!this.user.phone
        );
      },
      addressValid: function() {
        return (
          !!this.user.billingStreet &&
          // !!this.user.billingStreet2 &&
          !!this.user.billingCity &&
          !!this.user.billingState &&
          !!this.user.billingZip
        );
      },
      emailValid: function() {
        return /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(this.user.email);
      },
      ...mapState([
        'userInfo',
      ]),
      ...mapGetters([
        'currentSlide',
      ]),
    },
    methods: {

      // Data
      //--------
      cloneData: function(original) {
        return JSON.parse(JSON.stringify(original));
      },
      copyUserData: function() {
        this.user = this.cloneData(this.userInfo);
      },
      startEdit: function() {
        this.editing = true;
      },
      saveEdit: function() {
        this.$store.commit('saveUserInfo', this.cloneData(this.user));
        this.editing = false;
      },
      resetData: function() {
        this.copyUserData();
        this.editing = false;
      },

      showSuccessMessage: function() {
        this.$store.commit('showToast', 'Info Confirmed');
        this.editing = false;
      },

      // Same address toggle
      onSameAddressChange: function(e) {
        this.useSameAddress = e.target.checked;

        if (!e.target.checked) {
          this.editing = true;
        }
      },

      updatePhone: function(e) {
        // Format phone number to (000) 000-0000
        //------
        const phoneNumbersOnly = e.target.value.replace(/\D/g, '');
        const phoneSplit = phoneNumbersOnly.match(/(\d{0,3})(\d{0,3})(\d{0,4})/);

        const newVal = !phoneSplit[2] ? 
          phoneSplit[1] : 
          `(${phoneSplit[1]}) ${phoneSplit[2]}${ (phoneSplit[3]) ? '-'+phoneSplit[3] : ''}`;

        this.user.phone = newVal;
      },

      nextSlide: function() {
        this.$router.push(`/app/${this.currentSlide + 1}`);
      }
    },
    beforeMount: function() {
      this.copyUserData();
    },
  };

</script>



