

export function hidePhone(phone) {
  const lastFour = phone.substr(-4);
  return `(***) ***-${lastFour}`;
};

export function hideEmail(email) {
  const atLocation = email.indexOf('@');
  const endStr = email.substr((atLocation - 3), email.length);
  const starCount = email.length - endStr.length;
  return `${'*'.repeat(starCount)}${endStr}`;
};