
<template>

  <div class="slide-account">

    <div class="slide-content-wrapper kite-container">
      <div class="content">

          <section class="section-credentials">
            <div class="section-content">
              <h1>Create Your Username and Password</h1>
              <p class="kite-lead">You'll use your Spectrum username to sign in to your account at Spectrum.net.</p>


              <form class="password-form">

              <label for="username" class="input-group">
                <span class="label">Username</span>
                <input
                  type="text"
                  id="username"
                  name="username"
                  @keyup="validateUsername"
                  v-model="username"
                  class="kite-form-control"
                  :class="{
                    'kite-password-confirmed': usernameAcceptable,
                    'kite-form-control-danger': (usernameTouched && !usernameAcceptable),
                  }"
                  @focus="editingUsername"
                  @blur="endEditingUsername"
                >
                <span
                  class="danger-message"
                  :class="{
                    show: (usernameTouched && !usernameAcceptable),
                  }"
                >
                  {{usernameError}}
                </span>

                  <span
                    class="input-rules-position"
                    :class="{
                      show: showUsernameRules
                    }"
                  >
                    <span class="input-rules">
                      <h4>Please enter a username that follows these guidelines:</h4>

                      <rule-row
                        copy="Keep length between 6 and 40 characters."
                        :valid="usernameIsCorrectLength"
                      ></rule-row>
                      <rule-row
                        copy="Letters, numbers, periods, dashes, and underscores only."
                        :valid="usernameAcceptableChars"
                      ></rule-row>
                      <rule-row
                        copy="Include at least one letter."
                        :valid="usernameHasOneLetter"
                      ></rule-row>
                      <rule-row
                        copy="Start and end with a number or letter."
                        :valid="usernameStartEndLetterNumber"
                      ></rule-row>
                      <rule-row
                        copy="Don't use consecutive special characters."
                        :valid="usernameNoConsecutiveSpecials"
                      ></rule-row>

                    </span>
                  </span>


              </label>


                <label
                  for="password"
                  :class="{
                    verified: !!password,
                  }"
                >
                  <span class="label">Password</span>
                  <input
                    type="password"
                    id="password"
                    v-model="password"
                    class="kite-form-control"
                    :class="{
                      'kite-password-confirmed': passwordAcceptable,
                      'kite-form-control-danger': (passwordTouched && !passwordAcceptable)
                    }"
                    @keydown="validatePassword"
                    @focus="editingPassword"
                    @blur="endEditingPassword"
                    autocomplete="off"
                  />
                  <span
                    class="danger-message"
                    :class="{
                      show: (passwordTouched && !passwordAcceptable),
                    }"
                  >
                    {{passwordError}}
                  </span>

                  <span
                    class="input-rules-position"
                    :class="{
                      show: showPasswordRules
                    }"
                  >
                    <span class="input-rules">
                      <h4>Please enter a password that follows these guidelines:</h4>

                      <rule-row
                        copy="Keep length between 8 and 20 characters."
                        :valid="passIsCorrentLength"
                      ></rule-row>
                      <rule-row
                        copy="Include a number, uppercase letter and lowercase letter."
                        :valid="(passHasNumber && passHasCases)"
                      ></rule-row>
                      <rule-row
                        copy="Letters, numbers and select special characters only (no slashes, quotation marks or the > symbol)."
                        :valid="passCorrectCharacters"
                      ></rule-row>
                      <rule-row
                        copy="Dont include your username or any of the following terms: charter, spectrum, password, default, changeme, 123, abc, or xyz."
                        :valid="true"
                      ></rule-row>
                    </span>
                  </span> <!-- end .input-rules -->
                </label>

                <label
                  for="confirmPassword"
                >
                  <span class="label">Confirm Password</span>
                  <input
                    type="password"
                    id="confirmPassword"
                    v-model="confirmPassword"
                    class="kite-form-control"
                    :class="{
                      'kite-password-confirmed': passwordsMatch,
                      'kite-form-control-danger': (confirmPassword.length > 7) && !passwordsMatch
                    }"
                    autocomplete="off"
                  />
                  <span
                    class="danger-message"
                    :class="{
                      show: ((confirmPassword.length > 7) && !passwordsMatch),
                    }"
                  >
                    The passwords you entered don't match. Please enter them again.
                  </span>
                </label>

                <button
                  class="kite-btn kite-btn-primary kite-btn-lg"
                  :disabled="!enableSubmit"
                  @click.prevent.stop="nextSlide"
                >
                  Continue
                </button>
              </form>
            </div>
          </section>


      </div>
    </div>

  </div>

</template>



<script>

  import RuleRow from '../tooltip/input-rule-row.vue';
  import { mapState, mapGetters } from 'vuex';


  export default {
    name: 'slide-account-password',
    components: {
      RuleRow
    },
    props: [],
    data: function() {
      return {
        username: '',
        password: '',
        confirmPassword: '',

        usernameTouched: false,
        passwordTouched: false,

        showUsernameRules: false,
        usernameError: 'This username doesn\'t meet our guidelines. Please enter a different one.',

        showPasswordRules: false,
        passwordError: 'Please enter your password.',
      };
    },
    computed: {

      // Can Continue
      //----------
      enableSubmit: function() {
        return (
          this.usernameAcceptable &&
          this.passwordAcceptable &&
          this.passwordsMatch
        );
      },

      // Password Validation
      //----------
      passIsCorrentLength: function() {
        return (this.password.length >= 8 && this.password.length <= 20);
      },
      passHasNumber: function() {
        return /\d/.test(this.password);
      },
      passHasCases: function() {
        return /(?=[A-Z]*[a-z])(?=[a-z]*[A-Z])/.test(this.password);
      },
      passCorrectCharacters: function() {
        return /^[^\/>"]+$/.test(this.password);
      },
      passwordAcceptable: function() {
        return (
          this.passIsCorrentLength &&
          this.passHasCases &&
          this.passHasNumber &&
          this.passCorrectCharacters
        );
      },
      passwordsMatch: function() {
        return (this.password != '') && (this.password === this.confirmPassword);
      },

      // Username Validation
      //--------
      usernameIsCorrectLength: function() {
        return (this.username.length > 5 && this.username.length < 41);
      },
      usernameAcceptableChars: function() {
        return /^[\w.-]+$/.test(this.username);
      },
      usernameHasOneLetter: function() {
        return /[a-z]/i.test(this.username);
      },
      usernameStartEndLetterNumber: function() {
        return /^[a-z0-9].*[a-z0-9]$/i.test(this.username);
      },
      usernameNoConsecutiveSpecials: function() {
        return (
          this.username.length &&
          !/[._-](?=[._-])/.test(this.username)
        );
      },
      usernameAcceptable: function() {
        if (this.username === 'taken') {
          return false;
        } else {
          return (
            this.usernameIsCorrectLength &&
            this.usernameAcceptableChars &&
            this.usernameHasOneLetter &&
            this.usernameStartEndLetterNumber &&
            this.usernameNoConsecutiveSpecials
          );
        }
      },

      ...mapState([
        'userInfo',
      ]),
      ...mapGetters([
        'currentSlide',
      ])
    },
    methods: {
      editingPassword: function() {
        this.showPasswordRules = true;
      },
      endEditingPassword: function() {
        this.showPasswordRules = false;
        this.passwordTouched = true;
      },
      validatePassword: function(e) {
        if (!this.passHasCases || !this.passHasNumber || !this.passIsCorrentLength) {
          this.passwordError = 'This password doesn\'t meet our guidelines. Please enter a different one.';
          return false;
        }

        this.passwordError = '';
        return true;
      },
      showSuccessMessage: function() {
        this.$scope.commit('showToast', 'Username Created');
      },

      editingUsername: function() {
        this.showUsernameRules = true;
      },
      endEditingUsername: function() {
        this.showUsernameRules = false;
        this.usernameTouched = true;
      },
      validateUsername: function(e) {
        if (e.target.value === 'taken') {
          this.usernameError = 'This username is unavailable. Please enter a different one.';
        }
      },

      nextSlide: function() {
        this.$store.commit('saveUserCredentials', this.username, this.password);
        this.$router.push(`/app/${this.currentSlide+1}`);
      },

    }

  };

</script>



<style lang="scss">

@import "~@kite/shared/variables";
@import "~@kite/shared/mixins";

// @include kite-media-breakpoint-up(md) { }


.section-credentials {
  em {
    color: $kite-blue-2;
