    max-width: 480px;

    .kite-custom-control {
      margin-left: 0.25rem;
    }

    label {
      position: relative;
      display: block;
      margin-bottom: 22px;

      &.kite-custom-checkbox {
        margin-bottom: 20px;
      }
    }

    .small {
      margin-bottom: 16px;
    }
  }

  // Guidelines popovers
  //-----------
  .input-rules-position {
    opacity: 0;
    display: block;
    width: 100%;
    height: auto;
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.5s;

    .input-rules {
      display: block;
      padding: 16px;
      background-color: #fff;
      border-radius: 4px;
      box-shadow: 1px 1px 6px #aaa;
      pointer-events: none;
      transition: 0.6s all;
    }

    h4 {
      margin-bottom: 12px;
    }

    &.show {
      max-height: 340px;
      opacity: 1;
      padding-top: 14px;
      padding-bottom: 15px;
    }

    @include kite-media-breakpoint-up(md) {
      position: absolute;
      top: 65%;
      left: 104%;
      transform: translateY(-50%);
      opacity: 0;
      overflow: visible;

      display: inline-block;
      width: 330px;
      height: auto;
      max-height: none;
      pointer-events: none;
      transition: 0.3s all;

      &.show {
        max-height: none;
        opacity: 1;
      }

      .input-rules {
        &:before,
        &:after {
          content: '';
          display: inline-block;
          position: absolute;
          top: 50%;
          right: 100%;
          transform: translateY(-50%);
        }
        &:before {
          height: 0;
          width: 0;
          border-bottom: 8px solid transparent;
          border-top: 8px solid transparent;
          border-right: 10px solid #fff;
        }
        &:after {
          right: 94%;
          z-index: -1;
          height: 20px;
          width: 20px;
          box-shadow: -9px 7px 6px 0px #aaa;
          transform: rotate(45deg) translateY(-50%);
        }
      }

    }
  } // end .input-rules
}

</style>
