
<template>

  <div class="verify-code-input">
    <input
      type="number"
      class="kite-form-control"
      name="digit-1"
      id="digit-1"
      max="9"
      v-model="digit1"
      @keyup="checkAndNext"
    />
    <input
      type="number"
      class="kite-form-control"
      name="digit-2"
      id="digit-2"
      max="9"
      v-model="digit2"
      @keyup="checkAndNext"
    />
    <input
      type="number"
      class="kite-form-control"
      name="digit-3"
      id="digit-3"
      max="9"
      v-model="digit3"
      @keyup="checkAndNext"
    />
    <input
      type="number"
      class="kite-form-control"
      name="digit-4"
      id="digit-4"
      max="9"
      v-model="digit4"
      @keyup="checkAndNext"
    />
    <input
      type="number"
      class="kite-form-control"
      name="digit-5"
      id="digit-5"
      max="9"
      v-model="digit5"
      @keyup="checkAndNext"
    />
    <input
      type="number"
      class="kite-form-control"
      name="digit-6"
      id="digit-6"
      max="9"
      v-model="digit6"
      @keyup="checkAndNext"
    />
  </div>


</template>



<script>

  export default {
    name: 'verify-code-input',
    props: [
      'onChange',
    ],
    data: function() {
      return {
        digit1: '',
        digit2: '',
        digit3: '',
        digit4: '',
        digit5: '',
        digit6: '',
      };
    },
    computed: {
      entireCode: function() {
        return this.digit1 + this.digit2 + this.digit3 + this.digit4 + this.digit5 + this.digit6;
      },
    },
    watch: {
      entireCode: function(newVal) {
        return this.onChange(newVal);
      },
      digit1: function(newVal) {
        if (newVal.length > 1) {
          this.digit1 = newVal.substr(-1);
        }
      },
      digit2: function(newVal) {
        if (newVal.length > 1) {
          this.digit2 = newVal.substr(-1);
        }
      },
      digit3: function(newVal) {
        if (newVal.length > 1) {
          this.digit3 = newVal.substr(-1);
        }
      },
      digit4: function(newVal) {
        if (newVal.length > 1) {
          this.digit4 = newVal.substr(-1);
        }
      },
      digit5: function(newVal) {
        if (newVal.length > 1) {
          this.digit5 = newVal.substr(-1);
        }
      },
      digit6: function(newVal) {
        if (newVal.length > 1) {
          this.digit6 = newVal.substr(-1);
        }
      },
    },
    methods: {
      isNumber: function(val) {
        return /^[0-9]$/.test(val);
      },
      checkAndNext: function(e) {
        const keyIsNumber = this.isNumber(parseInt(e.key));
        const valueIsNumber = this.isNumber(e.target.value);

        if (keyIsNumber && valueIsNumber && e.target.nextElementSibling) {
          e.target.nextElementSibling.focus();
        }
        if (e.keyCode === 13) {
          e.preventDefault();
          return false;
        }
      }

    },
  };

</script>



<style lang="scss">

@import "~@kite/shared/variables";
@import "~@kite/shared/mixins";

// @include kite-media-breakpoint-up(md) { }


.verify-code-input {
  display: flex;
  align-content: stretch;

  input {
    display: inline-block;
    width: auto;
    min-width: 0;
    flex: 1;
    padding: 16px 4px;
    margin: 0 6px;
    text-align: center;
    font-size: 2rem;

    &[type=number] {
      -moz-appearance: textfield;

      &::-webkit-inner-spin-button, 
      &::-webkit-outer-spin-button { 
        -webkit-appearance: none; 
        margin: 0; 
      }
    }
  }

  @include kite-media-breakpoint-up(md) {
    input {
      margin: 0 12px;
      padding: 24px 8px;
      font-size: 2.5rem;
    }
  }
}

</style>
