
<!--
// This component handles the slide-change logic
 -->

<template>

  <div
    class="slide"
    :class="[
      isCurrent ? 'current' : '',
      isPast ? 'past' : '',
      isLast ? 'last' : '',
      slideClass,
    ]"
  >
    <slot></slot>
  </div>

</template>



<script>

  export default {
    name: 'slide-base',
    props: [
      'thisSlide',
      'currentSlide',
      'totalSlides',
    ],
    data: function() {
      return {
        slideClass: 'slide-'+ this.thisSlide,
        isLast: (this.thisSlide === this.totalSlides-1),
      };
    },
    computed: {
      isCurrent: function() {
        return (this.currentSlide === this.thisSlide);
      },
      isPast: function() {
        return (this.currentSlide > this.thisSlide);
      },
    }
  };

</script>



