
<template>

  <div class="toast">
    <button
      class="kite-btn kite-btn-primary kite-btn-lg"
      @click.prevent.stop="save"
      :disabled="disabled"
    >
      <transition name="name-fade">
        <span
          v-if="!isSaving"
        >
          {{btnCopy}}
        </span>
        <span
          v-if="isSaving"
        >
          <div class="loader">
            <svg class="circular" viewBox="25 25 50 50">
              <circle class="path" cx="50" cy="50" r="20" fill="none" stroke-width="3" stroke-miterlimit="10"/>
            </svg>
          </div>
        </span>
      </transition>
    </button>
  </div>

</template>



<script>

  export default {
    name: 'toast',
    props: [
      'afterSpin',
      'showToast',
      'btnCopy',
      'disabled',
    ],
    components: {
    },
    data: function() {
      return {
        isSaving: false,
      };
    },
    computed: {

    },
    methods: {
      save: function() {
        this.isSaving = true;

        const timeoutLength = Math.round((Math.random() * 1000) + 500); // between 0.5 and 1.5 seconds

        window.setTimeout(() => {

          this.afterSpin();
          this.showToast(); // TODO: Better way of coupling with showing notification

          window.setTimeout(() => {
            this.isSaving = false;
          }, 1000);
        }, timeoutLength);
      }
    },
  };

</script>



<style lang="scss">

  @import "~@kite/shared/mixins";


  .toast {
    display: inline-block;
    width: 100%;

    @include kite-media-breakpoint-up(md) {
      width: auto;
    }
  }

</style>
