
<template>

  <div class="billing-tip-imgs">

    <transition name="fast-fade" mode="out-in">
      <div
        v-if="paymentInfo.paymentMethod === 'checking' || paymentInfo.paymentMethod === 'savings'"
        class="check-img"
        key="1"
      >
        <img
          :src="require('../../assets/img/autopay-check.svg')"
          alt="routing and account number helper img"
        />


        <template v-if="paymentInfo.paymentMethod === 'checking'">
          <div class="check-routing routing">
            {{paymentInfo.checkRouting || 'XXXXXXXX'}}

            <div class="lower-label">
              <span class="label">Routing #</span>
            </div>
          </div>

          <div class="check-account account">
            {{paymentInfo.checkAccount || 'XXXXXXXXXXX'}}

            <div class="lower-label">
              <span class="label">Account #</span>
            </div>
          </div>
        </template>


        <template v-if="paymentInfo.paymentMethod === 'savings'">
          <div class="check-routing routing">
            {{paymentInfo.savingRouting || 'XXXXXXXX'}}

            <div class="lower-label">
              <span class="label">Routing #</span>
            </div>
          </div>

          <div class="check-account account">
            {{paymentInfo.savingAccount || 'XXXXXXXXXXX'}}

            <div class="lower-label">
              <span class="label">Account #</span>
            </div>
          </div>
        </template>


      </div>


      <div
        v-if="paymentInfo.paymentMethod === 'credit'"
        class="credit-img"
        key="2"
      >
        <img
          :src="require('../../assets/img/autopay-card.svg')"
          alt="credit card helper img"
        />
        <div class="credit-number" v-html="creditNumber"></div>
        <div class="credit-exp">{{paymentInfo.cardExpiration || '00/00'}}</div>

        <div class="card-logo">
          <transition name="fast-fade">
            <img
              v-if="paymentInfo.creditCardType === 'visa'"
              :src="require('../../assets/img/autopay-logo-visa.svg')"
              alt="visa logo"
              class="visa"
            />
            <img
              v-if="paymentInfo.creditCardType === 'amex'"
              :src="require('../../assets/img/autopay-logo-amex.svg')"
              alt="amex logo"
              class="amex"
            />
            <img
              v-if="paymentInfo.creditCardType === 'mastercard'"
              :src="require('../../assets/img/autopay-logo-mastercard.svg')"
              alt="mastercard logo"
              class="mastercard"
            />
            <img
              v-if="paymentInfo.creditCardType === 'discover'"
              :src="require('../../assets/img/autopay-logo-discover.svg')"
              alt="discover logo"
              class="discover"
            />
          </transition>
        </div>
      </div>
    </transition>

  </div>

</template>



<script>

  import { mapState } from 'vuex';


  export default {
    name: 'billing-img',
    components: {
    },
    props: [],
    data: function() {
      return {
      };
    },
    computed: {
      ...mapState([
        'testingVersion',
        'paymentInfo',
      ]),
      creditNumber: function() {
        if (!this.paymentInfo.card) {
          return '0000 0000 0000 0000';
        } else {
          let newCard = '';

          this.paymentInfo.card.split('').forEach((char) => {
            newCard += `<span class="digit">${char}</span>`;
          });

          return newCard;
        }
      }
    },
    methods: {
    },
  };

</script>



