
<template>

  <modal-base
    :title="modalTitle"
    cta-copy="Get Started"
    :can-show="canShowWelcomeModal"
    :hide="hideModal"
    :cta-action="hideModal"
    :class-name="modalClass"
  >
    <div slot="content" class="welcome-modal-content">
      <h4>Let's get you all set up.<br>This should take just a few minutes.</h4>
      <p>After you're done, you can connect to Spectrum WiFi hotspots, watch TV on your favorite devices and manage your account online.</p>
    </div>
  </modal-base>

</template>



<script>

  import ModalBase from './modal-base.vue';
  import { mapState } from 'vuex';


  export default {
    name: 'welcome-modal',
    components: {
      ModalBase,
    },
    props: [],
    data: function() {
      return {
        modalClass: 'welcome-modal',
      };
    },
    computed: {
      modalTitle: function() {
        return `Hi ${this.userInfo.name.split(' ')[0]}, Welcome to Spectrum`;
      },
      ...mapState([
        'canShowWelcomeModal',
        'userInfo'
      ]),
    },
    methods: {
      hideModal: function() {
        this.$store.commit('hideWelcomeModal');
      }
    },
  };

</script>



