
<template>

  <modal-base
    class-name="payment-modal"
    title="Complete Auto Pay Enrollment"
    :can-show="canShowPaymentSpeedbumpModal"
    :hide="onCancel"
    cta-copy="Enroll in Auto Pay"
    :cta-action="onEnroll"
    secondary-cta-copy="Skip Auto Pay Enrollment"
    :secondary-cta-action="onSkip"
  >
    <div slot="content" class="payment-speedbump-modal-content">
      <p>It looks like you entered a payment method. Before moving on, let us know if you want to enroll in Auto Pay.</p>
    </div>
  </modal-base>

</template>



<script>

  import ModalBase from './modal-base.vue';
  import { mapState, mapGetters } from 'vuex';


  export default {
    name: 'payment-speedbump-modal',
    components: {
      ModalBase,
    },
    props: [
    ],
    data: function() {
      return {
        paymentValue: '193.10'
      };
    },
    methods: {
      onSkip: function() {
        this.$store.commit('hidePaymentSpeedbumpModal');
        this.$router.push(`/app/${this.currentSlide+1}`);
      },
      onEnroll: function() {
        this.$store.commit('hidePaymentSpeedbumpModal');
        this.$store.commit('showPaymentConfirmModal');
      },
      onCancel: function() {
        this.$store.commit('hidePaymentSpeedbumpModal');
      }
    },
    computed: {
      ...mapState([
        'canShowPaymentSpeedbumpModal',
      ]),
      ...mapGetters([
        'currentSlide',
      ]),
    }
  };

</script>



