
<template>

  <div class="landing">

  <!-- Choose-Your-Path -->
    <div
      class="center-me"
      :class="{show: !showEmail}"
    >
      <div
        class="configure-container"
      >
        <div class="content-wrapper">
          <h2>Configure the Prototype</h2>


          <div class="group-title">Entry</div>
          <div class="input-group radio-group">
            <label class="kite-custom-control kite-custom-radio" for="dynamic-entry">
              <input
                id="dynamic-entry"
                name="entry"
                type="radio"
                class="kite-custom-control-input"
                value="dynamic-entry"
                :checked="useDynamicEntry"
                @change="onEntryChange"
              >
              <span class="kite-custom-control-indicator"></span>
              <span class="kite-custom-control-description">
                <span class="title">Email/Dynamic URL</span>
              </span>
            </label>

            <label class="kite-custom-control kite-custom-radio" for="sign-in-entry">
              <input
                id="sign-in-entry"
                name="entry"
                type="radio"
                class="kite-custom-control-input"
                value="sign-in-entry"
                :checked="!useDynamicEntry"
                @change="onEntryChange"
              >
              <span class="kite-custom-control-indicator"></span>
              <span class="kite-custom-control-description">
                <span class="title">Sign In/Vanity URL</span>
              </span>
            </label>
          </div>


          <div class="group-title">Expired</div>
          <label class="kite-custom-control kite-custom-checkbox input-group" for="expired">
            <span class="kite-custom-control-description">
              Experience is Expired?
            </span>
            <input
              id="expired"
              name="expired"
              type="checkbox"
              class="kite-custom-control-input"
              :checked="startIsExpired"
              @change="onExpiredChange"
            />
            <span class="kite-custom-control-indicator"></span>
          </label>


          <div class="group-title">Starting Point</div>
          <label
            for="starting-point"
            class="input-group"
            :class="{disabled: useDynamicEntry || startIsExpired}"
          >
            <span class="label">Select Section</span>
            <select
              name="starting-point"
              id="starting-point"
              :value="startingPoint"
              @change="setStartingPoint"
            >
              <option value="0"></option>
              <option value="3">3 - Contact</option>
              <option value="4">4 - Autopay</option>
              <option value="5">5 - Billing</option>
              <option value="6">6 - Appointment</option>
              <option value="7">7 - Tour</option>
            </select>
          </label>


          <div class="group-title">Install Type</div>
          <label
            for="install-type"
            class="input-group"
            :class="{disabled: startIsExpired}"
          >
            <span class="label">Choose type of install</span>
            <select
              name="install-type"
              id="install-type"
              :value="installType"
              @change="onInstallChange"
            >
              <option value="tech">Tech Install</option>
              <option value="self-kit">Self Install - kit</option>
              <option value="self-own">Self Install - own</option>
            </select>
          </label>


        </div>
      </div>


      <button
        class="kite-btn kite-btn-primary kite-btn-lg"
        @click="onSubmit"
      >
        Start
      </button>

    </div>


  <!-- Email -->
    <div
      class="center-me"
      :class="{show: showEmail}"
    >
      <h2>This is an Order Confirmation Email</h2>

      <router-link
        :to="emailLinkUrl"
        tag="button"
        class="kite-btn kite-btn-primary kite-btn-lg"
      >
        Go to Start
      </router-link>
    </div>

  </div>

</template>



<script>

  import SignIn from './sign-in.vue';
  import { mapState } from 'vuex';


  export default {
    name: 'configure',
    components: {
      SignIn
    },
    props: [],
    data: function() {
      return {
        showEmail: false,
        useDynamicEntry: true,
      };
    },
    computed: {
      emailLinkUrl: function() {
        return (this.startIsExpired) ? '/expired' : '/verify/1'
      },
      ...mapState([
        'installType',
        'startingPoint',
        'startIsExpired',
      ]),
    },
    methods: {

      // Configuration
      //---------
      onEntryChange: function(e) {
        this.useDynamicEntry = (e.target.value === 'dynamic-entry');
      },
      onExpiredChange: function(e) {
        this.$store.commit('setExpiredValue', e.target.checked);
      },
      onInstallChange: function(e) {
        this.$store.commit('setInstallType', e.target.value);
      },
      setStartingPoint: function(e) {
        this.$store.commit('setStartingPoint', e.target.value);
      },
      onSubmit: function() {
        // show email
        if (this.useDynamicEntry) {
          this.showEmail = true;

        // show sign in
        } else {
          this.$router.push(`/sign-in`);
        }
      },
    }
  }

</script>



