
<template>

  <div
    class="main-flow"
    :class="{
      'final-slide': onFinalSlide,
    }"
  >
    <div class="back-button"
      @click="() => { return $router.push(`/app/${currentSlide-1}`) }"
      :class="{
        hide: hideBackButton
      }"
    >
      <span class="back-arrow"></span>
    </div>


    <appt-modal></appt-modal>


    <welcome-modal></welcome-modal>


    <progress-modal></progress-modal>


    <payment-speedbump-modal></payment-speedbump-modal>
    <payment-confirm-modal></payment-confirm-modal>
    <payment-review-modal></payment-review-modal>
    <payment-error-modal></payment-error-modal>


    <sign-out-modal></sign-out-modal>


    <timeout-modal></timeout-modal>


    <start-header></start-header>


    <main
      class="main start-toolbar-fixed-adjust"
      :class="[
        `slide-${currentSlide}`
      ]"
      role="main"
    >
      <h1 class="kite-sr-only">Spectrum Start</h1>


      <transition
        v-if="!isMobile"
        name="bg-fade"
      >
        <div
          class="fixed-img"
          v-if="(showCheckInfoImage)"
          key="1"
        >
          <check-info-img></check-info-img>
        </div>

        <div
          class="fixed-img"
          v-if="(showPasswordImage)"
          key="11"
        >
          <username-img></username-img>
        </div>

        <img
          :src="require('../../assets/img/illustration-security_question.svg')"
          alt=""
          class="fixed-img"
          v-if="(showSecurityImage)"
          key="12"
        >

        <div
          class="fixed-img"
          v-if="(showCommunicationImage)"
          key="13"
        >
          <preferred-com-img></preferred-com-img>
        </div>

        <!-- Bill Img -->
        <div
          class="fixed-img"
          v-if="(showFirstBillImg)"
          key="21"
        >
          <billing-imgs></billing-imgs>
        </div>

        <div
          class="fixed-img"
          v-if="(showSecondBillImg)"
          key="3"
        >
          <paperless-img></paperless-img>
        </div>

        <!-- Appointment Img -->
        <div
          class="fixed-img"
          v-if="(showApptImg)"
          key="4"
        >
          <img
            v-if="(installType === 'tech')"
            :src="require('../../assets/img/illustration-tech_install.svg')"
            alt="illustration of delivery truck"
          >
          <img
            v-if="(installType === 'self-kit')"
            :src="require('../../assets/img/illustration-self_install.svg')"
            alt="illustration of box on doorstep"
          >
          <img
            v-if="(installType === 'self-own') || (installType === 'post-tech')"
            :src="require('../../assets/img/illustration-internet_only.svg')"
            alt="illustration of television"
          >
        </div>

      </transition>


      <slides></slides>
    </main>

    <toast-bar></toast-bar>
  </div>

</template>



<script>

  import StartHeader from '../header/start-header.vue';
  import ToastBar from '../toast/toast-bar.vue';
  import ProgressModal from '../modals/progress-modal.vue';
  import WelcomeModal from '../modals/welcome-modal.vue';
  import TimeoutModal from '../modals/timeout-modal.vue';
  import PaymentSpeedbumpModal from '../modals/payment-speedbump-modal.vue';
  import PaymentConfirmModal from '../modals/payment-confirmation-modal.vue';
  import PaymentReviewModal from '../modals/payment-review-modal.vue';
  import PaymentErrorModal from '../modals/payment-error-modal.vue';
  import ApptModal from '../modals/appt-modal.vue';
  import SignOutModal from '../modals/sign-out-modal.vue';

  import CheckInfoImg from '../svg/check-info.vue';
  import UsernameImg from '../svg/username-pw.vue';
  import PreferredComImg from '../svg/preferred-com.vue';
  import PaperlessImg from '../svg/paperless.vue';
  import BillingImgs from '../svg/billing-img.vue';

  import Slides from '../slides/slides.vue';

  import { mapState, mapGetters } from 'vuex';


  export default {
    name: 'main-flow',
    components: {
      StartHeader,
      Slides,
      ToastBar,
      ProgressModal,
      WelcomeModal,
      TimeoutModal,
      PaymentSpeedbumpModal,
      PaymentConfirmModal,
      PaymentReviewModal,
      PaymentErrorModal,
      ApptModal,
      SignOutModal,

      CheckInfoImg,
      UsernameImg,
      PreferredComImg,
      PaperlessImg,
      BillingImgs,
    },

    data: function() {
      return {
        timeoutCountdown: null,
      };
    },

    computed: {
      ...mapState([
        'installType',
        'isMobile',
      ]),
      ...mapGetters([
        'currentSlide',
        'onFinalSlide',
        'hideBackButton',

        'showCheckInfoImage',
        'showPasswordImage',
        'showSecurityImage',
        'showCommunicationImage',
        'showFirstBillImg',
        'showSecondBillImg',
        'showApptImg',
      ]),
    },
    methods: {

      // Timeout
      //--------
      startTimeout: function() {
        const timeout = 6000; //sec

        this.timeoutCountdown = window.setTimeout(() => {
          this.$store.commit('showTimeoutModal');
        }, (timeout * 1000));
      },
      resetTimeout: function() {
        window.clearTimeout(this.timeoutCountdown);
        this.startTimeout();
      },
      watchTimeout: function() {
        this.startTimeout();

        document.addEventListener('mousemove', (e) => {
          this.resetTimeout();
        });
      },
    },

    mounted: function() {
      // If on first slide, show welcome modal
      if (this.currentSlide === 0) {
        this.$store.commit('showWelcomeModal');
      } else if (this.currentSlide > 2) {
        this.$store.commit('showProgressModal');
      }

      // Watch timer for timeout
      this.watchTimeout();
    }

  };

</script>



<style lang="scss">

@import "~@kite/shared/mixins";


.main-flow {
  height: 100%;
}

.main {
  text-align: center;

  .fixed-img {
    display: none;

    @include kite-media-breakpoint-up(md) {
      display: inline-block;
      z-index: 0;
      position: absolute;
      left: 55%;
      top: 57%;
      width: 40%;
      max-width: 470px;
      height: auto;
      transform: translateY(-50%);
    }
  }
}

</style>
