
<template>

  <div class="tooltip">

    <!-- Question Mark -->
    <svg
      xmlns="http://www.w3.org/2000/svg"
      width="16" height="16" 
      viewBox="0 0 16 16"
      @click.stop.prevent="toggleTooltip"
    >
      <g fill-rule="nonzero">
        <path :fill="outlineColor" d="M8 0C3.584 0 0 3.584 0 8s3.584 8 8 8 8-3.584 8-8-3.584-8-8-8z"/>
        <path :fill="!canShowTooltip ? fillColor : outlineColor" d="M8 14.4c-3.528 0-6.4-2.872-6.4-6.4 0-3.528 2.872-6.4 6.4-6.4 3.528 0 6.4 2.872 6.4 6.4 0 3.528-2.872 6.4-6.4 6.4z"/>
        <path :fill="!canShowTooltip ? outlineColor : fillColor" d="M8 3.2c-1.768 0-3.2 1.432-3.2 3.2h1.6c0-.88.72-1.6 1.6-1.6.88 0 1.6.72 1.6 1.6 0 1.6-2.4 1.4-2.4 4h1.6c0-1.8 2.4-2 2.4-4 0-1.768-1.432-3.2-3.2-3.2z"/>
        <path :fill="!canShowTooltip ? outlineColor : fillColor" d="M7.2 12.8h1.6v-1.6H7.2v1.6z"/>
      </g>
    </svg>

    <!-- Tooltip -->
    <div
      class="tip-bubble-bg"
      :class="{show: canShowTooltip}"
      @click.stop="onBgClick"
    >
      <div
        class="tip-bubble"
        v-html="tip"
        @click.stop="() => {}"
      >
      </div>
    </div>
  </div>

</template>



<script>

  import SpinnerButton from '../inputs/spinner-button.vue';
  import SvgStartLogo from '../header/logo.vue';
  import { mapGetters } from 'vuex';


  export default {
    name: 'tooltip',
    components: {
    },
    props: {
      tip: {
        type: String,
        default: '',
      },
      isLight: {
        type: Boolean,
        default: false,
      }
    },
    data: function() {
      return {
        canShowTooltip: false,
      };
    },
    computed: {
      fillColor: function() {
        return (this.isLight) ? '#fff' : '#002133';
      },
      outlineColor: function() {
        return (this.isLight) ? '#0062b2' : '#fff';
      },
      ...mapGetters([
        'isMobile',
      ]),
    },
    methods: {
      hideTooltip: function() {
        this.canShowTooltip = false;
      },
      showTooltip: function() {
        this.canShowTooltip = true;
        document.addEventListener('click', this.onCloseClick)
      },
      toggleTooltip: function() {
        if (this.canShowTooltip) {
          this.hideTooltip();
        } else {
          this.showTooltip();
        }
      },
      onCloseClick: function(e) {
        this.hideTooltip();
        document.removeEventListener('click', this.onCloseClick);
      },
      onBgClick: function(e) {
        if (this.isMobile) {
          this.onCloseClick();
        } else {
          e.stopPropagation();
          e.preventDefault();
          return false;
        }
      },

    },
  };

</script>



<style lang="scss">

