      pointer-events: auto;
    }

    @include kite-media-breakpoint-up(md) {
      position: absolute;
      left: calc(100% + 14px);
      top: 50%;
      width: 290px;
      height: auto;
      text-align: left;
      background-color: transparent;
      border-radius: 4px;
      box-shadow:  0 1px 5px 0 rgba(99,115,138,.4);
      transform: translateY(-50%);
      pointer-events: none;

      &.show {
        top: 50%;
        opacity: 1;
        pointer-events: auto;
      }
    }
  }

  .tip-bubble {
    position: absolute;
