
<template>

  <button
    class="kite-btn kite-btn-primary kite-btn-lg spinner-button"
    type="button"
    @click.prevent.stop="spin"
    :disabled="disabled"
  >
    <transition name="name-fade">
      <span
        v-if="!showSpin"
      >
        {{btnCopy}}
      </span>
      <span
        v-if="showSpin"
      >
        <div class="loader">
          <svg class="circular" viewBox="25 25 50 50">
            <circle class="path" cx="50" cy="50" r="20" fill="none" stroke-width="3" stroke-miterlimit="10"/>
          </svg>
        </div>
      </span>
    </transition>
  </button>

</template>



<script>

  export default {
    name: 'spinner-button',
    props: [
      'afterSpin',  // fn
      'btnCopy',    // string
      'disabled',   // bool
    ],
    components: {
    },
    data: function() {
      return {
        showSpin: false,
      };
    },
    methods: {
      spin: function() {
        this.showSpin = true;

        // Fake a process/call
        window.setTimeout(() => {
          this.afterSpin();

          window.setTimeout(() => {
            this.showSpin = false;
          }, 2000);
        }, 1200);

      },
    },
  };

</script>



