<script>

  import ScrollWrapper from '../slides/scroll-wrapper.vue';
  import Watchlist from '../watchlist/watchlist.vue';

  import WifiMap from '../map/wifi-map.vue';
  import ChannelLineup from '../channel-lineup/channel-lineup.vue';
  import TvImg from '../svg/tv.vue';
  import GuideImg from '../svg/guide.vue';
  import DevicesFootballImg from '../svg/devices-football.vue';
  import NetLinkImg from '../svg/net-link.vue';

  import { mapState, mapGetters } from 'vuex';


  export default {
    name: 'slide-final',
    components: {
      ScrollWrapper,
      Watchlist,

      WifiMap,
      ChannelLineup,
      TvImg,
      GuideImg,
      DevicesFootballImg,
      NetLinkImg,
    },
    props: [
      'thisSlide',
    ],
    data: function() {
      return {
        initialScrollTimeout: null,
      };
    },
    watch: {
      currentSlide: function() {
        this.checkIfCurrent();
      },
    },
    computed: {
      ...mapState([
        'isMobile',
      ]),
      ...mapGetters([
        'totalSlides',
        'currentSlide',
      ]),
    },
    methods: {
      checkIfCurrent: function() {
        if (this.thisSlide === this.currentSlide) {
          // Let the header animate and hide, then trigger this animation
          window.setTimeout(() => {
            this.startAnimation(); // Takes 2.6s
          }, 3000);
        }
      },
      startAnimation: function() {
        // Use class to start animations
        this.$el.classList.add('start');
      },
      showSignoutModal: function() {
        this.$store.commit('showSignoutModal');
      },

    },
    mounted: function() {
      this.checkIfCurrent();
    }
  };

</script>



