
<template>

  <modal-base
    title="You're Almost Done"
    :class-name="modalClass"
    :can-show="canShowSignoutModal"
    :hide="hideSignoutModal"
    cta-copy="Complete Setup"
    :cta-action="hideSignoutModal"
    secondary-cta-copy="Sign Out"
    :secondary-cta-action="signOut"
  >
    <div slot="content" class="sign-out-modal-content">
      <p>You're only about a minute away from getting all set up and unlocking all the things you can do with your service.</p>
      <p>We'll save your progress so you can pick up where you left off when you come back.</p>
        <!-- to <a href="//start.spectrum.net">start.spectrum.net</a>.</p> -->

      <progress-checklist />

    </div>
  </modal-base>

</template>



<script>

  import ModalBase from './modal-base.vue';
  import ProgressChecklist from './progress-checklist.vue';
  import { mapState, mapGetters } from 'vuex';


  export default {
    name: 'sign-out-modal',
    components: {
      ModalBase,
      ProgressChecklist,
    },
    props: [],
    data: function() {
      return {
        modalClass: 'sign-out-modal',
      };
    },
    computed: {
      ...mapState([
        'canShowSignoutModal',
      ]),
      ...mapGetters([
        'currentSlide',
      ]),
    },
    methods: {
      hideSignoutModal: function() {
        this.$store.commit('hideSignoutModal');
      },
      signOut: function() {
        this.$store.commit('setStartingPoint', this.currentSlide);
        this.$router.push('/sign-in');
        this.hideSignoutModal();
      },
    },
  };

</script>



