
<template>

  <div class="verify">

    <div class="logo">
      <svg-start-logo
        fill="#fff"
      ></svg-start-logo>
    </div>

    <div class="content">

      <!-- Toggle correct view -->
      <transition
        name="fast-fade" 
        mode="out-in"
      >
        <verify-choice
          v-if="currentStep === 1"
          key="verify-choice"
          :set-verify-method="setVerifyMethod"
          :verify-method="verifyMethod"
          :hidden-user-phone="hiddenUserPhone"
          :hidden-user-email="hiddenUserEmail"
          :on-send-code="sendCode"
        ></verify-choice>
        <verify-code
          v-if="currentStep === 2"
          key="verify-code"
          :verify-method="verifyMethod"
          :hidden-user-phone="hiddenUserPhone"
          :hidden-user-email="hiddenUserEmail"
          :reset-choice="resetChoice"
        ></verify-code>
      </transition>

    </div>

    <toast-bar></toast-bar>

  </div>


</template>



<script>

  import VerifyChoice from '../verification/verify-choice.vue';
  import VerifyCode from '../verification/verify-code.vue';
  import SvgStartLogo from '../header/logo.vue';
  import ToastBar from '../toast/toast-bar.vue';
  import {hidePhone, hideEmail} from '../../utils/strings';

  import { mapState } from 'vuex';


  export default {
    name: 'verify',
    components: {
      VerifyChoice,
      VerifyCode,
      SvgStartLogo,
      ToastBar
    },
    props: [],
    data: function() {
      return {
        verifyMethod: '', // 'email', // vs 'text' vs 'call'
        showModal: false,
        showVerifyChoice: true,
        currentStep: null,
      };
    },
    computed: {
      hiddenUserPhone: function() {
        const {phone} = this.userInfo;
        return hidePhone(phone);
      },
      hiddenUserEmail: function() {
        const {email} = this.userInfo;
        return hideEmail(email);
      },
      ...mapState([
        'userInfo',
      ]),
    },
    watch: {
      '$route' (to, from) {
        const nextStep = parseInt(to.params.step);
        if (nextStep === 1 || nextStep === 2) {
          this.currentStep = nextStep;
        }
      }
    },
    methods: {

      // Verification
      //-------
      setVerifyMethod: function(e) {
        this.verifyMethod = e.target.value;
      },
      sendCode: function() {
        this.$router.push({
          params: {step: 2},
          query: {method: this.verifyMethod}
        });
      },
      resetChoice: function() {
        this.$router.push({params: {step: 1}});
      },
    },
    mounted: function() {
      if (!this.$route.params.step) {
        this.$router.push({params: {step: 1}});
      } else {
        this.currentStep = parseInt(this.$route.params.step);
      }
      

      // Set testing version
      if (this.$route.query.version === '2') {
        this.$store.commit('setTestingVersion', 'v2');
      }
    }
  };

</script>



