
<template>

  <div class="overlay-slider">
    <div
      v-show="canScrollLeft || isMobile"
      class="slider-arrow left"
      :class="{hover: overLeftArrow}"
      @click.prevent="sliderLeft"
      @mouseenter="overLeftArrow = true"
      @mouseleave="overLeftArrow = false"
    >
      <span
        class="arrow"
      ></span>
    </div>
    <div
      v-show="canScrollRight || isMobile"
      class="slider-arrow right"
      :class="{hover: overRightArrow}"
      @click.prevent="sliderRight"
      @mouseenter="overRightArrow = true"
      @mouseleave="overRightArrow = false"
    >
      <span
        class="arrow"
      ></span>
    </div>

    <div class="slider-window">
      <div
        class="slider-track"
        :style="`transform: translateX(${sliderPosition}px);`"
      >

        <slot></slot>

      </div>
    </div>
  </div> <!-- // End slider -->

</template>



<script>

  export default {
    name: 'slider',
    components: {
      // gridItem,
    },
    props: [
    ],
    data: function() {
      return {
        isMobile: (window.innerWidth < 600),
        overLeftArrow: false,
        overRightArrow: false,

        sliderPosition: 0,
        sliderScrollDistance: 0,
        sliderScrollWidth: 0,
        sliderWindowWidth: 0,
        maxScroll: 0,
      };
    },
    computed: {
      canScrollLeft: function() {
        return (this.sliderPosition < 0);
      },
      canScrollRight: function() {
        return (-this.sliderPosition < (this.sliderScrollWidth - this.sliderWindowWidth) );
      },
    },
    methods: {
      sliderLeft: function() {
        if (this.canScrollLeft) {
          this.sliderPosition += this.sliderScrollDistance;

          if (this.sliderPosition > 0) {
            this.sliderPosition = 0;
          }
        }
      },
      sliderRight: function() {
        if (this.canScrollRight) {
          this.sliderPosition -= this.sliderScrollDistance;

          if (this.sliderPosition < -this.maxScroll) {
            this.sliderPosition = -this.maxScroll;
          }
        }
      },
    },
    mounted: function() {

      // Initialize slider
      //-------
      if (!this.isMobile) {
        // - Get widths
        let sliderTrack = this.$el.querySelector('.slider-track');

        // - Save widths
        this.sliderWindowWidth = window.innerWidth;

        this.$nextTick(() => {
          this.sliderScrollWidth = sliderTrack.scrollWidth;

          // - Get scroll width
          this.sliderScrollDistance = window.innerWidth * 0.5; // this.sliderScrollWidth / Math.ceil(this.gridItems.length/8);
          this.maxScroll = sliderTrack.scrollWidth - this.sliderWindowWidth;
        });
      }
    }
  };

</script>



<style lang="scss">

@import "~@kite/shared/variables";
@import '~@kite/shared/mixins';

// @include kite-media-breakpoint-up(md) { }


.overlay-slider {
  position: relative;
  margin: 0 auto;

  .slider-arrow {
    position: absolute;
    z-index: 2;
    top: 0;
    display: inline-block;
    height: 100%;
    width: 30px;
    pointer-events: auto;
    cursor: pointer;

    .arrow {
      display: none;
    }

    &.right {
      right: 0;
      background-image: linear-gradient(to right, rgba(0,0,0, 0), rgba(0,0,0, 1));

      .arrow {
        left: auto;
        right: 15px;
        transform: translate(0%, -50%) rotate(45deg);
      }
    }
    &.left {
      left: 0;
      background-image: linear-gradient(to left, rgba(0,0,0, 0), rgba(0,0,0, 1));

      .arrow {
        left: 15px;
        right: auto;
        transform: translate(0%, -50%) rotate(-135deg);
      }
    }

    @include kite-media-breakpoint-up(md) {
      width: 100px;
      transition: all 0.5s cubic-bezier(0.48, 0.18, 0.22, 0.95);
      pointer-events: default;

      &.hover {
        width: 120px;
      }

      .arrow {
        display: inline-block;
        position: absolute;
        top: 50%;

        height: 50px;
        width: 50px;
        padding: 15px;
        transform-origin: 50% 50%;

        &:before {
          content: '';
          display: inline-block;
          width: 100%;
          height: 100%;
          border-top: 2px solid $kite-gray-2;
          border-right: 2px solid $kite-gray-2;
        }
      }
    } // end media

  }
  .slider-window {
    overflow: visible;
    width: 100%;

    @include kite-media-breakpoint-up(md) {
      // overflow: hidden;
    }

    .slider-track {
      position: relative;
      display: flex;
      flex-wrap: wrap;
      flex-direction: column;
      max-height: 400px;
      transition: all 0.4s cubic-bezier(0.45, 0.1, 0.22, 0.99);
    }
  }
}

</style>
