
<template>

  <div class="slide-account">

    <div class="slide-content-wrapper kite-container">
      <div class="content">


          <section class="section-contact">
            <div class="section-content">
              <h1 class="short">What's the Best Way&nbsp;to Contact You?</h1>

              <fieldset class="radios-container">
                <p class="kite-lead">Let us know how to contact you if something important happens with your account.</p>
                <label class="kite-custom-control kite-custom-radio" for="input-notification-email">
                  <input id="input-notification-email" name="notification" type="radio" class="kite-custom-control-input" value="email" v-model="notification">
                  <span class="kite-custom-control-indicator"></span>
                  <span class="kite-custom-control-description">
                    <span class="title">By Email</span>
                    <span class="label-detail">{{userInfo.email}}</span>
                  </span>
                </label>
                <label class="kite-custom-control kite-custom-radio" for="input-notification-text">
                  <input id="input-notification-text" name="notification" type="radio" class="kite-custom-control-input" value="text" v-model="notification">
                  <span class="kite-custom-control-indicator"></span>
                  <span class="kite-custom-control-description">
                    <span class="title">By Text</span>
                    <span class="label-detail">{{userInfo.phone}}</span>
                  </span>
                </label>
              </fieldset>

              <div class="action-container">
                <button
                  class="kite-btn kite-btn-primary kite-btn-lg"
                  @click="nextSlide" 
                  :disabled="!contactEnableNext"
                >
                  Continue
                </button>
              </div>
            </div>
          </section>


      </div>
    </div>

  </div>

</template>



<script>

  import { mapState, mapGetters } from 'vuex';


  export default {
    name: 'slide-account-contact',
    props: [],
    data: function() {
      return {
        notification: 'email',
      };
    },
    computed: {
      contactEnableNext: function() {
        return (
          !!this.notification
        );
      },
      ...mapState([
        'userInfo',
      ]),
      ...mapGetters([
        'currentSlide',
      ]),
    },
    methods: {
      nextSlide: function() {
        this.$router.push(`/app/${this.currentSlide+1}`);
      }
    }

  };

</script>



<style lang="scss">

@import "~@kite/shared/variables";
@import "~@kite/shared/mixins";

