
// import Vuex from 'vuex';

// Slides
import SlideAccountVerify from './components/views/account-verify.vue';
import SlideAccountPassword from './components/views/account-password.vue';
import SlideSecurityQuestion from './components/views/account-sec-question.vue';
import SlideAccountContact from './components/views/account-contact.vue';

import SlideBillingAutopay from './components/views/billing-autopay.vue';
import SlideBillingReciept from './components/views/billing-reciept.vue';

import SlideAppointment from './components/views/appointment.vue';
import SlideFinal from './components/views/final.vue';



const store = {
  state: {
    // Prototype configurations
    startIsExpired: false,
    installType: 'self-kit', // 'tech' vs 'self-kit' vs 'self-own' vs 'post-tech'
    startingPoint: 3,
    billStatus: 'balance', // vs 'none' or 'pastdue'

    canShowWelcomeModal: false,
    canShowProgressModal: false,
    canShowTimeoutModal: false,
    canShowPaymentConfirmModal: false,
    canShowPaymentSpeedbumpModal: false,
    canShowPaymentReviewModal: false,
    canShowPaymentErrorModal: false,
    canShowSignoutModal: false,
    canShowApptModal: false,

    // Watchlist
    showingWatchlistOverlay: false,

    // Toast
    showingToast: false,
    toastTimeout: null,
    toastMessage: 'Progress Saved',

    // App
    allSlides: [
      SlideAccountVerify,
      SlideAccountPassword,
      SlideSecurityQuestion,
      SlideAccountContact,

      SlideBillingAutopay,
      SlideBillingReciept,

      SlideAppointment,
      SlideFinal
    ],
    userInfo: {
      username: '',
      password: '',
      name: 'John Stanley',
      street: '3892 Broadway',
      city: 'Denver',
      state: 'CO',
      zip: '81234',
      email: 'jstan@gmail.com',
      phone: '(294) 218-1389',

      billingStreet: '3892 Broadway',
      billingStreet2: 'A405',
      billingCity: 'Denver',
      billingState: 'CO',
      billingZip: '81234',
    },
    skipFirstPayment: false,
    paymentInfo: {
      // useAutopay: false,
      paymentMethod: 'checking', // checking vs savings vs credit
      card: '',
      creditCardType: '',
      cardExpiration: '',
      checkAccount: '',
      checkRouting: '',
      savingAccount: '',
      savingRouting: '',
    },
  },


  getters: {
    currentSlide: (state) => {
      return parseInt(state.route.params.slide) || 0;
    },
    totalSlides: (state) => {
      return state.allSlides.length;
    },
    onFinalSlide: (state, getters) => {
      return (getters.currentSlide === getters.totalSlides-1);
    },
    hideBackButton: (state, getters) => {
      return getters.onFinalSlide || (getters.currentSlide === 0);
    },

    // Slide images
    //--------------
    showCheckInfoImage: function(state, getters) {
      return (getters.currentSlide === 0);
    },
    showPasswordImage: function(state, getters) {
      return (getters.currentSlide === 1);
    },
    showSecurityImage: function(state, getters) {
      return (getters.currentSlide === 2);
    },
    showCommunicationImage: function(state, getters) {
      return (getters.currentSlide === 3);
    },
    showFirstBillImg: function(state, getters) {
      return (getters.currentSlide === 4);
    },
    showSecondBillImg: function(state, getters) {
      return (getters.currentSlide === 5);
    },
    showApptImg: function(state, getters) {
      return (getters.currentSlide === 6);
    },

    // Utils
    //------------
    isMobile: function(state) {
      return (window.innerWidth < 600);
    },

    // Payment Dates
    //--------------
    today: function(state) {
      // get today's date
      const today = new Date();
      const year = `${today.getFullYear()}`;
      return `${(today.getMonth() + 1) % 12}/${today.getDate()}/${year.substring(2,4)}`;
    },
    paymentDate: function(state, getters) {
      const today = new Date();
      const year = `${today.getFullYear()}`;
      return `${(today.getMonth() + 2) % 12}/1/${year.substring(2,4)}`;
    },
    nextPaymentDate: function(state, getters) {
      const today = new Date();
      const year = `${today.getFullYear()}`;
      return `${(today.getMonth() + 3) % 12}/1/${year.substring(2,4)}`;
    },
    skipPaymentNextDate: function(state, getters) {
      const today = new Date();
      const year = `${today.getFullYear()}`;
      return `${(today.getMonth() + 4) % 12}/1/${year.substring(2,4)}`;
    },

  },


  mutations: {

    // Landing/Config
    //----------
    setStartingPoint: function(state, i) {
      state.startingPoint = parseInt(i);
    },
    setExpiredValue: function(state, bool) {
      state.startIsExpired = bool;
    },
    setInstallType: function(state, val) {
      state.installType = val;
    },
    setBillStatus: function(state, val) {
      state.billStatus = val;
    },
    setSkipFirstPayment: function(state, val) {
      state.skipFirstPayment = val;
    },

    // Accepts an object that has any or all of the properties found this this.userInfo, and will update with what it recieves
    saveUserInfo: function(state, newInfoObj) {
      for (let prop in newInfoObj) {
        if (state.userInfo[prop]){
          state.userInfo[prop] = newInfoObj[prop];
        }
      }
    },
    saveUserCredentials: function(state, username, password) {
      state.userInfo.username = username;
      state.userInfo.password = password;
    },


    // Modals
    //----------
    showWelcomeModal: function(state) {
      state.canShowWelcomeModal = true;
    },
    hideWelcomeModal: function(state) {
      state.canShowWelcomeModal = false;
    },

    showProgressModal: function(state) {
      state.canShowProgressModal = true;
    },
    hideProgressModal: function(state) {
      state.canShowProgressModal = false;
    },

    showTimeoutModal: function(state) {
      state.canShowTimeoutModal = true;
    },
    hideTimeoutModal: function(state) {
      state.canShowTimeoutModal = false;
    },

    showPaymentSpeedbumpModal: function(state) {
      state.canShowPaymentSpeedbumpModal = true;
    },
    hidePaymentSpeedbumpModal: function(state) {
      state.canShowPaymentSpeedbumpModal = false;
    },

    showPaymentConfirmModal: function(state) {
      state.canShowPaymentConfirmModal = true;
    },
    hidePaymentConfirmModal: function(state) {
      state.canShowPaymentConfirmModal = false;
    },

    showPaymentReviewModal: function(state) {
      state.canShowPaymentReviewModal = true;
    },
    hidePaymentReviewModal: function(state) {
      state.canShowPaymentReviewModal = false;
    },

    showPaymentErrorModal: function(state) {
      state.canShowPaymentErrorModal = true;
    },
    hidePaymentErrorModal: function(state) {
      state.canShowPaymentErrorModal = false;
    },

    showSignoutModal: function(state) {
      state.canShowSignoutModal = true;
    },
    hideSignoutModal: function(state) {
      state.canShowSignoutModal = false;
    },

    showApptModal: function(state) {
      state.canShowApptModal = true;
    },
    hideApptModal: function(state) {
      state.canShowApptModal = false;
    },



    // Watchlist Overlay
    //----------
    setShowingWatchlistOverlay: function(state, val) {
      state.showingWatchlistOverlay = val;
    },


    // Slide Progress
    //----------
    previousSlide: function(state) {
      // prevent going too far back
      if (state.currentSlide != 0) {
        state.currentSlide--;
      }
    },
    nextSlide: function(state) {
      // Prevent going too far forward
      if (state.currentSlide < (state.allSlides.length - 1) ) {
        state.currentSlide++;
      }
    },
    reset: function(state) {
      state.canShowLanding = true;
      state.currentSlide = 0;
    },
    signOut: function() {
      state.startingPoint = state.currentSlide;
    },


    // Toast
    //----------
    showToast: function(state, newMessage) {
      if (newMessage) {
        state.toastMessage = newMessage;
      }
      state.showingToast = true;

      // Show for 5s
      state.toastTimeout = window.setTimeout(() => {
        state.showingToast = false;
      }, 4500);
    },
    hideToast: function(state) {
      state.showingToast = false;
      window.clearTimeout(state.toastTimeout);
    },


    // Auto Pay Inputs
    //----------
    setPaymentMethod: function(state, newVal) {
      state.paymentInfo.paymentMethod = newVal;
    },
    setPaymentCardValue: function(state, newVal) {
      state.paymentInfo.card = newVal;
    },
    setPaymentCardExp: function(state, newVal) {
      state.paymentInfo.cardExpiration = newVal;
    },
    setPaymentCardType: function(state, newVal) {
      state.paymentInfo.creditCardType = newVal;
    },
    setCheckingRoute: function(state, newVal) {
      state.paymentInfo.checkRouting = newVal;
    },
    setCheckingAcct: function(state, newVal) {
      state.paymentInfo.checkAccount = newVal;
    },
    setSavingRoute: function(state, newVal) {
      state.paymentInfo.savingRouting = newVal;
    },
    setSavingAcct: function(state, newVal) {
      state.paymentInfo.savingAccount = newVal;
    },
  },
};

export default store;