
<template>

  <div class="slide-billing">

    <div class="slide-content-wrapper kite-container">
      <div class="content">

        <section class="section-payment">
          <div class="section-content">
            <h1>Go Paperless</h1>
            <p class="kite-lead">Reduce clutter and keep your personal info more secure by enrolling in paperless billing. You'll automatically get an email notification when your bill is ready to view online.</p>

            <div class="payment-reciept">
              <label for="use-paperless" class="kite-custom-control kite-custom-checkbox">
                <input
                  type="checkbox"
                  name="use-paperless"
                  id="use-paperless"
                  class="kite-custom-control-input"
                  value="usePaperless"
                  v-model="usePaperless"
                >
                <div class="kite-custom-control-indicator"></div>
                <span class="kite-custom-control-description">
                  <span class="label">Enroll in Paperless Billing</span>
                  <span class="label-detail">{{userInfo.email}}</span>
                </span>
              </label>
            </div>
            <!-- // end .payment-reciept -->

            <button
              v-if="!usePaperless"
              class="kite-btn kite-btn-primary kite-btn-lg"
              @click="nextSlide"
            >
              Continue
            </button>
            <toast-button
              v-if="usePaperless"
              :after-spin="nextSlide"
              :show-toast="showToast"
              btn-copy="Continue"
            ></toast-button>
          </div>
        </section>

      </div>
    </div>

  </div>


</template>



<script>

  import ToastButton from '../toast/toast-button.vue';
  import { mapState, mapGetters } from 'vuex';


  export default {
    name: 'slide-billing-reciept',
    components: {
      ToastButton,
    },
    props: [],
    data: function() {
      return {
        usePaperless: false,
      };
    },
    computed: {
      ...mapState([
        'userInfo',
      ]),
      ...mapGetters([
        'currentSlide',
      ]),
    },
    methods: {
      nextSlide: function() {
        this.$router.push(`/app/${this.currentSlide+1}`);
      },
      showToast: function() {
        this.$store.commit('showToast', 'Enrolled in Paperless Billing')
      }
    }
  };

</script>



