      img: require('../../assets/img/channels/ion.png'),
      favorite: false,
      category: 'local',
    },
    {
      name: 'PBS',
      number: 9,
      img: require('../../assets/img/channels/pbs.png'),
      favorite: false,
      category: 'local',
    },
    {
