    },
    {
      name: 'CSPAN',
      number: 16,
      img: require('../../assets/img/channels/cspan.png'),
      favorite: false,
      category: 'news',
    },
    {
      name: 'Univison',
      number: 17,
      img: require('../../assets/img/channels/univision.png'),
      favorite: false,
      category: 'local',
    },
    {
      name: 'SECN',
      number: 18,
      img: require('../../assets/img/channels/secn.png'),
      favorite: false,
      category: 'sports',
    },
    {
      name: 'POP',
      number: 19,
      img: require('../../assets/img/channels/pop.png'),
      favorite: false,
      category: 'local',
    },
    {
      name: 'Sundance',
      number: 20,
      img: require('../../assets/img/channels/sundance.png'),
      favorite: false,
      category: '',
    },
    {
      name: 'Hallmark',
      number: 26,
      img: require('../../assets/img/channels/hallmark.png'),
      favorite: false,
      category: '',
    },
    {
      name: 'USA',
      number: 28,
      img: require('../../assets/img/channels/usa.png'),
      favorite: false,
      category: 'popular',
    },
    {
      name: 'Lifetime',
      number: 29,
      img: require('../../assets/img/channels/lifetime.png'),
      favorite: false,
      category: '',
    },
    {
      name: 'HGTV',
      number: 30,
      img: require('../../assets/img/channels/hgtv.png'),
      favorite: false,
      category: 'popular',
    },
    {
      name: 'AMC',
      number: 31,
      img: require('../../assets/img/channels/amc.png'),
      favorite: false,
      category: 'popular',
    },
    {
      name: 'ESPN',
      number: 35,
      img: require('../../assets/img/channels/espn.png'),
      favorite: false,
      category: 'sports',
    },
    {
      name: 'CNN',
      number: 38,
      img: require('../../assets/img/channels/cnn.png'),
      favorite: false,
      category: 'news',
    },
  ];


  export default {
    name: 'channel-lineup',
    components: {
    },
    props: [
    ],
    data: function() {
      return {
        channels: CHANNELS,
        sortType: 'number', // vs 'name'
        filterTerm: '',
        filterCategory: '',
      };
    },
    computed: {
    },
    methods: {
      filter: function(e) {
        this.filterTerm = e.target.value;
        const filterIsNumber = !isNaN( parseInt(this.filterTerm) );

        this.channels = CHANNELS.filter((channel) => {

          if (filterIsNumber) {
            const re = new RegExp('^' + parseInt(this.filterTerm));
            return re.test(channel.number);
          } else {
            return (channel.name.toLowerCase().indexOf(this.filterTerm.toLowerCase()) != -1);
          }
        });

        this.updateSort(this.sortType);
      },
      updateSort: function(newSort) {
        this.sortType = newSort;

        if (newSort === 'name') {
          this.channels.sort((a, b) => {
            const nameA = a.name.toLowerCase();
            const nameB = b.name.toLowerCase();
            if (nameA < nameB) {
              return -1;
            }
            if (nameA > nameB) {
              return 1;
            }
            return 0;
          });

        } else {
          this.channels.sort((a, b) => {
            return a.number - b.number;
          });
        }

      },
      favoriteChannel: function(channel) {
        channel.favorite = !channel.favorite;
        this.$store.commit('showToast', `${channel.name} Added to Favorites`);
      },
      categoryFilter: function(e) {
        this.filterCategory = e.target.value;

        if (this.filterCategory != '') {
          this.channels = CHANNELS.filter((channel) => {
            return (channel.category === this.filterCategory);
          });
        } else {
          this.channels = CHANNELS;
        }

        this.updateSort(this.sortType);
      },
    },

  };

</script>



