
<template>

  <div class="slide-scrolling">

    <div class="scroll-container">
      <slot
        name="content"
        :section="currentSection"
      ></slot>
    </div>

    <nav
      v-if="!isMobile"
      class="vertical-dots"
      :class="{hide: showingWatchlistOverlay}"
    >
      <div
        class="nav-dot-container"
        v-for="(section, i) in sections"
        @click="currentSection = parseInt(i)"
      >
        <div
          class="nav-dot"
          :class="{
            current: (currentSection == i),
            past: (currentSection > i),
          }"
        >
          <span class="label">
            {{dotLabels[i]}}
          </span>
        </div>
      </div>
    </nav>

    <button
      class="circle-icon down-button"
      :class="{hide: (this.currentSection === this.sections.length-1) || showingWatchlistOverlay}"
      @click="downClick"
    >
      <span class="inner-icon">
        <span class="bounce-container">
          <span class="down-arrow"></span>
        </span>
      </span>
    </button>

  </div>

</template>



<script>

import { mapState } from 'vuex';
import throttle from 'lodash.throttle';

// Scrolling
//-----
// On show ( in watch.currentSlide() ):
// - Real scrolling is locked by overflow:hidden in css
// - In mounted(), get and store sections and their scroll positions
// - Then watch 'wheel' event on window
//    - On event, transform the slide content to show next/previous section
//    - When at start or end of section, trigger slide change


const scrollConfig = {
  scrollThreshold: 5,
  touchScrollThreshold: 20,
};

const labels = [
  '',
  'WiFi Hotspots',
  'Watch',
  'Channels',
  'Watchlist',
  'Account',
];


export default {
  name: 'scroll-wrapper',
  props: [
    'thisSlide',
    'currentSlide',
  ],
  data: function() {
    return {
      scrollInitiated: false,
      isShowing: this.currentSlide === this.thisSlide,
      scrollStops: {},
      sections: [],
      currentSection: 0,
      scrollElement: {},
      isMobile: (window.innerWidth < 600),
      dotLabels: labels,
    };
  },
  watch: {
    currentSlide: function() {
      this.checkIfCurrent();
    },
    currentSection: function() {
      this.changeSection();
    },
    showingWatchlistOverlay: function(showing) {
      if (showing) {
        // disable scroll
        this.eventsOff();
      } else {
        // enable scroll
        this.eventsOn();
      }
    },
  },
  computed: {
    ...mapState([
      'showingWatchlistOverlay'
    ]),
  },
  methods: {
    downClick: function() {
      this.cancelScrollTimeout();
      this.nextSection();
    },

    //-----------
    // Scroll functions
    //-----------
    init: function() {
      if (!this.isMobile) {

        this.scrollInitiated = true;

        // Store the scroll positions of <section>s
        this.sections = this.$el.querySelectorAll('section');
        this.setScrollStops();

        // Cache the scrolled element and sections
        this.scrollElement = this.$el.querySelector('.scroll-container');

        // If showing, init the scroll
        this.checkIfCurrent();


      } else { // Is mobile
        this.scrollElement = this.$el.parentElement.parentElement;

        // If showing, init the scroll
        this.checkIfCurrent();
      }
    },

    setScrollStops: function() {
      let scrollStops = {};

      this.sections.forEach((el, i) => {
        scrollStops[i] = el.offsetTop;
      });

      this.scrollStops = scrollStops;
    },
    handleResize: function() {
      this.isMobile = (window.innerWidth < 600);

      if (this.isMobile){
        this.destroyScrollWatch();
        this.scrollInitiated = false;

      } else {
        if (!this.scrollInitiated) {
          this.init();
        } else {
          // Update scroll stops
          this.setScrollStops();

          // Re-center section
          this.setSectionPosition();
        }
      }
    },
    checkIfCurrent: function() {
      this.isShowing = (this.thisSlide === this.currentSlide);

      if (this.isMobile) {
        if (this.isShowing) {
          this.initMobileScrollWatch();
        } else {
          this.destroyMobileScrollWatch();
        }
      } else {
        if (this.isShowing) {
          // this.setScrollTimeout(); // Automatically jump to next
          this.initScrollWatch();
          this.showCurrentSection();
        } else {
          this.destroyScrollWatch();
        }
      }
    },

    setScrollTimeout: function() {
      this.scrollTimeout = window.setTimeout(() => {
        this.nextSection();
      }, 10000);
    },
    cancelScrollTimeout: function() {
      if (this.scrollTimeout) {
        window.clearTimeout(this.scrollTimeout);
      }
    },

    initScrollWatch: function() {
      // Give time for intro animation to end
      window.setTimeout(() => {
        this.setScrollStops();
        this.eventsOn();
      }, 5000);
    },
    initMobileScrollWatch: function() {
      window.setTimeout(() => {
        this.mobileEventsOn();
      }, 800);
    },
    destroyScrollWatch: function() {
      this.eventsOff();
    },
    destroyMobileScrollWatch: function() {
      this.mobileEventsOff();
    },

    eventsOn: function() {
      window.addEventListener('wheel', this.wheelWatcher, {passive: true});
      this.scrollElement.addEventListener('touchstart', this.touchWatcher, {passive: true});
    },
    eventsOff: function() {
      window.removeEventListener('wheel', this.wheelWatcher);
      this.scrollElement.removeEventListener('touchstart', this.touchWatcher);
    },
    mobileEventsOn: function() {
      this.scrollElement.addEventListener('touchend', this.mobileTouchWatcher);
    },
    mobileEventsOff: function() {
      this.scrollElement.removeEventListener('touchend', this.mobileTouchWatcher);
    },
    pauseEvents: function() {
      // pause scroll-watcher while animation takes place
      this.eventsOff();
      window.setTimeout(() => {
        this.eventsOn();
      }, 1000);
    },
    wheelWatcher: function(e) {
      // Don't trigger if scrolling left/right
      if (Math.abs(e.deltaX) > Math.abs(e.deltaY)) {
        return;
      }

      // Ensure scroll is over arbitrary sensitivity threshold
      if (Math.abs(e.deltaY) > scrollConfig.scrollThreshold) {
        if (e.deltaY > 0) {
          this.nextSection();
        } else {
          this.previousSection();
        }
      }
    },
    touchWatcher: function(e) {

      // watch for direction of touchmove event after touchdown.  If up, previousSection(), if down, nextSection()
      const event = e.touches[0];
      const startX = event.clientX;
      const startY = event.clientY;

      //-----------
      // TODO: Check if scrolling left/right
      //-----------

      const thisTouchEndHandler = touchEndHandler.bind(this);

      this.scrollElement.addEventListener('touchend', thisTouchEndHandler);


      function touchEndHandler(e) {
        // e.preventDefault();
        const event = e.changedTouches[0];
        const newX = event.clientX;
        const newY = event.clientY;

        const deltaY = newY - startY;
        const up = (deltaY > (scrollConfig.touchScrollThreshold) );
        const down = (deltaY < -(scrollConfig.touchScrollThreshold) );

        if (up) {
          this.previousSection();
        } else if (down) {
          this.nextSection();
        }

        this.scrollElement.removeEventListener('touchend', thisTouchEndHandler);
      }
    },
    mobileTouchWatcher: function(e) {

      // Just watch for ends of the slide, 
      // - then trigger next() or previous() if at end or beginning

      const elHeight = this.scrollElement.scrollHeight;
      const containerHeight = this.scrollElement.parentElement.offsetHeight;
      const scrollPosition = this.scrollElement.scrollTop;

      const isAtTop = (scrollPosition === 0);
      const isAtBottom = (scrollPosition + containerHeight === elHeight);
    },
    nextSection: function() {
      this.cancelScrollTimeout();

      // Determine if at end of sections, and if so, go to next slide
      if (this.currentSection === this.sections.length-1) {
        return;
      }

      // Else, go to next section
      this.currentSection++;
    },
    previousSection: function() {
      // Determine if at the first section, and if so, go to previous slide
      if (this.currentSection === 0) {
        return;
      }

      // Else, go to previous section
      this.currentSection--;
    },
    setSectionPosition: function() {
      // Transform the scrollElement by the distance determined in mounted()
      this.scrollElement.style.transform = `translateY(-${this.scrollStops[this.currentSection]}px)`;
    },
    changeSection: function() {
      this.setSectionPosition();

      // Wait for animation
      this.pauseEvents();

      // fade-in current section
      this.showCurrentSection();
    },
    showCurrentSection: function() {
      this.sections.forEach((el, i) => {
        el.classList.toggle('past', i < this.currentSection);
        el.classList.toggle('current', i === this.currentSection);
      });
    }

  }, // end methods //

  mounted: function() {
    this.$nextTick(() => {
      this.init();

      window.addEventListener('resize', throttle(this.handleResize, 500) );
    });
  },

};

</script>



