
<template>

  <modal-base
    title="Welcome Back, John"
    :cta-copy="modalCtaCopy"
    :can-show="canShowProgressModal"
    :hide="hideModal"
    :cta-action="hideModal"
    :class-name="className"
  >
    <div slot="content" class="progress-modal-content">
      <template v-if="currentSlide != 7">
        <h4>Let's finish getting you all set up.</h4>
        <p>After you're done, you can connect to Spectrum WiFi hotspots, watch TV on your favorite devices and manage your account online.</p>
      </template>

      <template v-if="currentSlide === 7">
        <p>You're all set up. Now, let's explore all of the things you can do with your service.</p>
      </template>

      <progress-checklist />
    </div>
  </modal-base>

</template>



<script>

  import ModalBase from './modal-base.vue';
  import ProgressChecklist from './progress-checklist.vue';
  import { mapState, mapGetters } from 'vuex';
  

  export default {
    name: 'progress-modal',
    components: {
      ModalBase,
      ProgressChecklist,
    },
    props: [],
    data: function() {
      return {
        className: 'progress-modal',
      };
    },
    computed: {
      ...mapState([
        'canShowProgressModal'
      ]),
      ...mapGetters([
        'currentSlide'
      ]),
      modalCtaCopy: function() {
        if (this.currentSlide === 7) {
          return 'Explore Services';
        } else {
          return 'Complete Setup';
        }
      }
    },
    methods: {
      hideModal: function() {
        this.$store.commit('hideProgressModal');
      }
    }
  };

</script>



<style lang="scss">

@import "~@kite/shared/variables";
@import "~@kite/shared/mixins";

