<script>
  
  import { mapState, mapGetters } from 'vuex';


  export default {
    name: 'slide-appointment',
    props: [],
    data: function() {
      return {
        addToCalendar: '',
      };
    },
    computed: {
      ...mapState([
        'userInfo',
        'installType',
      ]),
      ...mapGetters([
        'currentSlide'
      ]),
    },
    methods: {
      nextSlide: function() {
        this.$router.push(`/app/${this.currentSlide+1}`);
      },
      onInstallChange: function(e) {
        this.$store.commit('setInstallType', e.target.value)
      },

      onCalendarAdd: function(e) {
        this.$store.commit('showToast', `Added to your ${e.target.value} calendar`);
        this.addToCalendar = '';
      },
      changeAppointment: function(e) {
        this.$store.commit('showApptModal');
      }
    }
  };

</script>



