
<template>

  <div class="verify-code">
    <div class="feature-image">
      <img v-if="$route.query.method === 'text'" src="../../assets/img/illustration-text.svg" alt="device illustration">
      <img v-if="$route.query.method === 'email'" src="../../assets/img/illustration-email.svg" alt="device illustration">
      <img v-if="$route.query.method === 'call'" src="../../assets/img/illustration-call.svg" alt="device illustration">
    </div>

    <h1>Enter Verification Code</h1>
    <p class="action-text" v-html="actionText"></p>

    <form name="verify-input" autocomplete="off">
      <label for="verify-code">
        <!-- <span class="label">Verification Code</span> -->
        <span
          class="danger-message"
          :class="{
            show: showError,
          }"
        >
          {{errorCode}}
        </span>
        <verify-code-input
          :on-change="onInputChange"
        ></verify-code-input>
      </label>

      <div class="alternate-actions">
        <button
          type="button"
          class="kite-btn kite-btn-link"
          @click="showResendToast"
        >
          Resend Code
        </button>
        <button
          type="button"
          class="kite-btn kite-btn-link"
          @click="resetChoice"
        >
          Verify Another Way
        </button>
      </div>

      <div class="action-container">
        <spinner-button
          btn-copy="Submit"
          :disabled="!enableSubmit"
          :after-spin="onSubmitCode"
        ></spinner-button>
      </div>
    </form>
  </div>

</template>



<script>

  import SpinnerButton from '../inputs/spinner-button.vue';
  import VerifyCodeInput from './verify-code-input.vue';


  export default {
    name: 'verify-code',
    components: {
      SpinnerButton,
      VerifyCodeInput,
    },
    props: [
      'hiddenUserPhone',
      'hiddenUserEmail',
      'resetChoice',
    ],
    data: function() {
      return {
        verifyCode: '',
        enableSubmit: false,
        showError: false,
        errorCode: 'Please enter a valid 6-digit verification code.',
      };
    },
    computed: {
      contactMethod: function() {
        if (this.$route.query.method === 'call' || this.$route.query.method === 'text') {
          return this.hiddenUserPhone;
        } else {
          return this.hiddenUserEmail;
        }
      },
      actionText: function() {
        switch (this.$route.query.method) {
          case 'call':
            return `We called ${this.contactMethod} with a verification code.`;
            break;
          case 'text':
            return `We texted a verification code to <span class="nobreak">${this.contactMethod}<span>.`;
            break;
          case 'email':
            return `We emailed a verification code to ${this.contactMethod}.`;
            break;
        }
      },
    },
    methods: {
      onSubmitCode: function() {
        this.$router.push('/app/0');
      },
      checkInput: function(e) {

        switch (this.verifyCode) {
          case 'error':
            this.errorCode = 'Please enter a valid 6-digit verification code.';
            this.showError = true;
            break;

          case 'a lot':
            this.errorCode = 'You\'ve exceeded the maximum number of attempts. Please try again.';
            this.showError = true;
            break;

          default:
            this.showError = false;
        }

      },
      onInputChange: function(newVal) {
        this.verifyCode = newVal;

        if (newVal.length === 6) {
          this.enableSubmit = true;

          // Focus on submit button
          this.$nextTick(() => {
            const btn = this.$el.querySelector('.spinner-button');
            btn.focus();
          });
        }
      },
      showResendToast: function(e) {
        this.$store.commit('showToast', 'Verification Code Sent');
      }
    },

    mounted: function() {
      // if no verify method in query, just set to text
      if (!this.$route.query.method) {
        this.$router.push({query: {method: 'text'}});
      }
    }
  };

</script>



