
<template>
  
  <nav
    class="kite-tab-bar"
    :class="{
      complete: currentSlide === (totalSlides-1)
    }"
  >

    <div class="dots">
      <div
        class="dot-container"
        v-for="n in (totalSlides-1)"
        :key="n"
      >
        <div
          class="dot"
          :class="{
            current: (n-1 === currentSlide),
            past: (n-1 < currentSlide),
            big: (n === 1 || n === 4 || n === 7),
          }"
        >
          <!-- Checkmark -->
          <svg class="icon-check animated" width="6" height="5" viewBox="0 0 6 5" xmlns="http://www.w3.org/2000/svg">
            <path d="M1 2.09l1.546 1.606L5.43 1" stroke="#008516" fill="none" fill-rule="evenodd"/>
          </svg>
        </div>
      </div>
    </div>

    <div class="progress">
      <div
        class="progress-bar"
        role="progressbar"
        :style="barStyle"
        :aria-valuenow="ariaPercent"
        aria-valuemin="0"
        aria-valuemax="100"
      >
        <!-- <span class="label">{{progressPercent}}%</span> -->
      </div>
    </div>

    <div
      class="complete-message"
      :class="{
        show: currentSlide === (totalSlides-1)
      }"
    >
      Account Setup Complete
    </div>

  </nav>

</template>



<script>

  import { mapGetters } from 'vuex';

  export default {
    name: 'tab-bar',
    props: [],
    data: function() {
      return {
        totalSections: 0,
      };
    },
    computed: {
      progressPercent: function() {
        return Math.round((this.currentSlide)/(this.totalSlides-2) * 100);
      },
      ariaPercent: function() {
        return this.progressPercent + ' percent complete';
      },
      barStyle: function() {
        return `width: ${this.progressPercent}%;'`
      },
      ...mapGetters ([
        'totalSlides',
        'currentSlide',
      ]),
    },
    methods: {
      isCurrent: function(n) {
        return (n === this.currentSlide);
      },
      isPast: function(n) {
        return (n < this.currentSlide);
      },
    }
  };

</script>



